import * as React from 'react';

import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro
} from './styled';
import { Footer } from './Footer';

import { getRenderedPost } from '../common/Helpers';

import { Strings } from '../strings';

import { IEnv } from './Wizard';

export interface IInstallationTabProps {
  env: IEnv;
}

export interface IInstallationTabState {
  html: string;
}

export class InstallationTab extends React.Component<
  IInstallationTabProps,
  IInstallationTabState
> {
  public constructor(props: IInstallationTabProps) {
    super(props);
    this.state = {
      html: Strings.empty
    };
  }

  public componentDidMount(): void {
    getRenderedPost('how-to-install-wordpress-office-365-login-plugin').then(
      (html: string | Error) => {
        if (html instanceof Error) {
          console.log(html.message);
          return;
        }
        this.setState({ html });
      }
    );
  }

  public render(): React.ReactElement<IInstallationTabProps> {
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>Installation Guide</StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{ __html: this.state.html }}
          />
        </StyledTableCaption>
        <Footer />
      </StyledTable>
    );
  }
}
