import * as React from 'react';

import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledRowifier,
  StyledCheckbox,
  StyledErrorFor,
  StyledP,
  StyledTextArea
} from './styled';

import { ValidatableTextInput } from './ValidatableTextInput';
import { SettingsException } from '../common/Exceptions';
import { Footer } from './Footer';
import { ReplaceOrAddRoles, SaveStatus, AuthScenario, PluginVersion, Size } from '../common/Enums';
import { CalloutButtonWp } from './CalloutButtonWp';
import { Strings } from '../strings';
import { KeyValuePair } from '../common/KeyValuePair';
import { Settings } from '../common/Settings';

import { Checkbox } from 'office-ui-fabric-react/lib/Checkbox';
import { Dropdown, IDropdownOption, DropdownMenuItemType } from 'office-ui-fabric-react/lib/Dropdown';
import { PrimaryButton } from 'office-ui-fabric-react/lib/Button';
import { errorFor } from '../common/Helpers';
import { ListEditor } from './ListEditor';
import { IEnv } from './Wizard';
import { KeyValueEditorChoice } from './KeyValueEditorChoice';
import { VersionSwitch } from './VersionSwitch';
import { IAdditionalInfoDialogProps } from '../common/IAdditionalInfoDialogProps';
import { AdditionalInfoDialog } from './AdditionalInfoDialog';

export interface IUserRegistrationTabProps {
  env: IEnv;
  availableRoles: string[];
  settings: Settings;
}

export interface IUserRegistrationTabState {
  blockEmailChange: boolean;
  blockPasswordChange: boolean;
  createAndAddUsers: boolean;
  customDomain: string[];
  domainsXRoles: KeyValuePair[];
  defaultDomain: string;
  defaultRoleAsFallback: boolean;
  groupsWhitelist: string[];
  groupsXRoles: KeyValuePair[];
  multiTenanted: boolean;
  muNewUsrDefaultRole: string;
  newUsrDefaultRole: string;
  newUsrSendMail: boolean;
  newUsrSendMailAdminOnly: boolean;
  newUsrSendMailCustom: boolean;
  newUsrMailSubject: string;
  newUsrMailTitle: string;
  newUsrMailSalutation: string;
  newUsrMailBody: string;
  newUsrMailFooter: string;
  replaceOrUpdateUserRoles: ReplaceOrAddRoles;
  welcomePageUrl: string;
  // ---
  errors: SettingsException[];
  saveStatus: SaveStatus;
  additionalInfo: IAdditionalInfoDialogProps | null;
}

export class UserRegistrationTab extends React.Component<IUserRegistrationTabProps, IUserRegistrationTabState> {
  public constructor(props: IUserRegistrationTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      blockEmailChange: settings.blockEmailChange,
      blockPasswordChange: settings.blockPasswordChange,
      createAndAddUsers: settings.createAndAddUsers,
      customDomain: settings.customDomain,
      domainsXRoles: settings.domainsXRoles,
      defaultDomain: settings.defaultDomain,
      defaultRoleAsFallback: settings.defaultRoleAsFallback,
      groupsWhitelist: settings.groupsWhitelist,
      groupsXRoles: settings.groupsXRoles,
      multiTenanted: settings.multiTenanted,
      muNewUsrDefaultRole: settings.muNewUsrDefaultRole,
      newUsrDefaultRole: settings.newUsrDefaultRole,
      newUsrSendMail: settings.newUsrSendMail,
      newUsrSendMailAdminOnly: settings.newUsrSendMailAdminOnly,
      newUsrSendMailCustom: settings.newUsrSendMailCustom,
      newUsrMailSubject: settings.newUsrMailSubject,
      newUsrMailTitle: settings.newUsrMailTitle,
      newUsrMailSalutation: settings.newUsrMailSalutation,
      newUsrMailBody: settings.newUsrMailBody,
      newUsrMailFooter: settings.newUsrMailFooter,
      replaceOrUpdateUserRoles: settings.replaceOrUpdateUserRoles,
      welcomePageUrl: settings.welcomePageUrl,
      errors: [],
      saveStatus: SaveStatus.initial,
      additionalInfo: null
    };
  }

  public componentDidUpdate(prevProps: IUserRegistrationTabProps): void {
    if (this.props.settings === prevProps.settings) return;
    const { settings } = this.props;
    this.setState({
      blockEmailChange: settings.blockEmailChange,
      blockPasswordChange: settings.blockPasswordChange,
      createAndAddUsers: settings.createAndAddUsers,
      customDomain: settings.customDomain,
      defaultDomain: settings.defaultDomain,
      defaultRoleAsFallback: settings.defaultRoleAsFallback,
      domainsXRoles: settings.domainsXRoles,
      groupsWhitelist: settings.groupsWhitelist,
      groupsXRoles: settings.groupsXRoles,
      multiTenanted: settings.multiTenanted,
      muNewUsrDefaultRole: settings.muNewUsrDefaultRole,
      newUsrDefaultRole: settings.newUsrDefaultRole,
      newUsrSendMail: settings.newUsrSendMail,
      newUsrSendMailAdminOnly: settings.newUsrSendMailAdminOnly,
      newUsrSendMailCustom: settings.newUsrSendMailCustom,
      newUsrMailSubject: settings.newUsrMailSubject,
      newUsrMailTitle: settings.newUsrMailTitle,
      newUsrMailSalutation: settings.newUsrMailSalutation,
      newUsrMailBody: settings.newUsrMailBody,
      newUsrMailFooter: settings.newUsrMailFooter,
      replaceOrUpdateUserRoles: settings.replaceOrUpdateUserRoles,
      welcomePageUrl: settings.welcomePageUrl
    });
  }

  public render(): React.ReactElement<IUserRegistrationTabProps> {
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>User registration</StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{
              __html: `Use this tab to configure what users can register with your WordPress website, how they can
            register, what role is assigned to them - possibly mapped with their Office 365 Group Membership -
            by default and whether or not they can change their email address and password.`
            }}
          />
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Custom domain</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ListEditor
                list={this.state.customDomain}
                onListChanged={(list: string[]) => this.setState({ customDomain: list })}
                placeholder={Strings.infoCustomDomain}
                isValid={new RegExp(/^(?!http)/i)}
                errorMessage={Strings.errCustomDomainCannotStartWith}>
                <CalloutButtonWp
                  wpSlug={'your-own-domain'}
                  info={{
                    title: 'Your (own) domain',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/your-own-domain/'
                    }
                  }}
                />
              </ListEditor>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Default domain</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  return (newValue as string).length > 0
                    ? /^\w+\.onmicrosoft.com$/.test(newValue as string) // At least two words, separated by a .
                    : true;
                }}
                onValid={(newValue: any) => {
                  this.setState({ defaultDomain: newValue as string });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errDefaultDomainMustBeTld}
                defaultValue={this.state.defaultDomain}
                value={this.state.defaultDomain}
                placeholder={Strings.infoDefaultDomain}>
                <CalloutButtonWp
                  wpSlug={'default-domain'}
                  info={{
                    title: 'Default Domain',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/default-domain/'
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Allow users from other tenants</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox onChange={this.toggleMultiTenanted} checked={this.state.multiTenanted} />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={'allow-users-from-other-tenants'}
                    info={{
                      title: 'Allow users from other tenants',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/allow-users-from-other-tenants/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Create new users</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox onChange={this.toggleCreateAndAddUsers} checked={this.state.createAndAddUsers} />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={'create-new-users'}
                    info={{
                      title: 'Create new users',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/create-new-users/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Welcome page</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  isValid={(oldValue: any, newValue: any) => {
                    if ((newValue as string) != Strings.empty)
                      return /^(http|https):\/\//i.test(newValue as string);
                    return true;
                  }}
                  onValid={(newValue: any) => {
                    this.setState({
                      welcomePageUrl: newValue as string
                    });
                    return;
                  }}
                  bound={false}
                  errorMessage={Strings.errWelcomePageUrlMustStartWith}
                  defaultValue={this.state.welcomePageUrl}
                  value={this.state.welcomePageUrl}>
                  <CalloutButtonWp
                    wpSlug={'welcome-page-url'}
                    info={{
                      title: 'Welcome Page',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/welcome-page-url/'
                      }
                    }}
                  />
                </ValidatableTextInput>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Send new user notification</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox onChange={this.toggleNewUsrSendMail} checked={this.state.newUsrSendMail} />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={'new-user-email'}
                    info={{
                      title: 'New user notification email',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/new-user-email/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Send only to admin</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleNewUsrSendMailAdminOnly}
                      checked={this.state.newUsrSendMailAdminOnly}
                    />
                  </StyledCheckbox>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Send using custom template</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleNewUsrSendMailCustom}
                      checked={this.state.newUsrSendMailCustom}
                    />
                  </StyledCheckbox>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>New user notification subject</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => {
                    this.setState({ newUsrMailSubject: newValue as string });
                    return;
                  }}
                  bound={false}
                  defaultValue={this.state.newUsrMailSubject}
                  value={this.state.newUsrMailSubject}
                  placeholder={Strings.infoNewUserMailSubject}
                  disabled={!this.state.newUsrSendMailCustom}
                />
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>New user notification title</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => {
                    this.setState({ newUsrMailTitle: newValue as string });
                    return;
                  }}
                  bound={false}
                  defaultValue={this.state.newUsrMailTitle}
                  value={this.state.newUsrMailTitle}
                  placeholder={Strings.infoNewUserMailTitle}
                  disabled={!this.state.newUsrSendMailCustom}
                />
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>New user notification salutation</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => {
                    this.setState({ newUsrMailSalutation: newValue as string });
                    return;
                  }}
                  bound={false}
                  defaultValue={this.state.newUsrMailSalutation}
                  value={this.state.newUsrMailSalutation}
                  placeholder={Strings.infoNewUserMailSalutation}
                  disabled={!this.state.newUsrSendMailCustom}
                />
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>New user notification body</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledTextArea
                  value={this.state.newUsrMailBody}
                  onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                    this.setState({ newUsrMailBody: event.target.value })
                  }
                  disabled={!this.state.newUsrSendMailCustom}
                />
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>New user notification footer</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledTextArea
                  value={this.state.newUsrMailFooter}
                  onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                    this.setState({ newUsrMailFooter: event.target.value })
                  }
                  disabled={!this.state.newUsrSendMailCustom}
                />
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>User cannot change email address</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox onChange={this.toggleBlockEmailChange} checked={this.state.blockEmailChange} />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={'user-cannot-change-email-address'}
                    info={{
                      title: 'User cannot change email address',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/user-cannot-change-email-address/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>User cannot change password</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox onChange={this.toggleBlockPasswordChange} checked={this.state.blockPasswordChange} />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={'user-cannot-change-password'}
                    info={{
                      title: 'User cannot change password',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/user-cannot-change-password/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Default role (main site)</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Dropdown
                    options={[
                      {
                        key: 'select',
                        text: 'Select',
                        itemType: DropdownMenuItemType.Header
                      },
                      ...this.props.availableRoles.map((role: string, idx: number) => {
                        const roleToLower = role.toLocaleLowerCase();
                        return {
                          key: roleToLower,
                          text: roleToLower
                        };
                      })
                    ]}
                    selectedKey={this.state.newUsrDefaultRole}
                    onChange={this.changeDefaultRole}
                    placeholder={Strings.infoNewUserDefaultRole}
                  />
                  <CalloutButtonWp
                    wpSlug={'default-role-main-site'}
                    info={{
                      title: 'Default role main site',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/default-role-main-site/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[
              PluginVersion.wpo365LoginProfessional,
              PluginVersion.wpo365LoginPremium,
              PluginVersion.wpo365LoginIntranet
            ]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Default role (wpmu sub site)</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Dropdown
                    options={[
                      {
                        key: 'select',
                        text: 'Select',
                        itemType: DropdownMenuItemType.Header
                      },
                      ...this.props.availableRoles.map((role: string, idx: number) => {
                        const roleToLower = role.toLocaleLowerCase();
                        return {
                          key: roleToLower,
                          text: roleToLower
                        };
                      })
                    ]}
                    selectedKey={this.state.muNewUsrDefaultRole}
                    onChange={this.changeMuDefaultRole}
                    placeholder={Strings.infoNewMuUserDefaultRole}
                  />
                  <CalloutButtonWp
                    wpSlug={'default-role-sub-site'}
                    info={{
                      title: 'Default role sub site',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/default-role-sub-site/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[PluginVersion.wpo365LoginPremium, PluginVersion.wpo365LoginIntranet]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Azure AD groups whitelist</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <ListEditor
                  list={this.state.groupsWhitelist}
                  onListChanged={(list: string[]) => this.setState({ groupsWhitelist: list })}
                  placeholder={Strings.infoGroupsWhiteList}>
                  <CalloutButtonWp
                    wpSlug={'groups-whitelist'}
                    info={{
                      title: 'Azure AD groups whitelist',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/groups-whitelist/'
                      }
                    }}
                  />
                </ListEditor>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[PluginVersion.wpo365LoginPremium, PluginVersion.wpo365LoginIntranet]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>(Group) Role mappings</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <KeyValueEditorChoice
                    placeholder={Strings.infoGroupsXRoles}
                    choice={this.props.availableRoles}
                    choicePlaceholder={Strings.infoWordPressRole}
                    list={this.reverseKeyValuePairList(this.state.groupsXRoles)}
                    onListChanged={(list: KeyValuePair[]) =>
                      this.setState({
                        groupsXRoles: this.reverseKeyValuePairList(list)
                      })
                    }
                    isValid={
                      new RegExp(/^[0-9a-f]{8}-[0-9a-f]{4}-[1-5][0-9a-f]{3}-[89ab][0-9a-f]{3}-[0-9a-f]{12}$/i)
                    }
                    errorMessage={Strings.errAadSGroupIdMustBeGuid}>
                    <CalloutButtonWp
                      wpSlug={'roles-mappings'}
                      info={{
                        title: '(Groups) role mappings',
                        readMore: {
                          title: 'Read online',
                          url: 'https://www.wpo365.com/roles-mappings/'
                        }
                      }}
                    />
                  </KeyValueEditorChoice>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[PluginVersion.wpo365LoginPremium, PluginVersion.wpo365LoginIntranet]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>(Domain) Role mappings</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <KeyValueEditorChoice
                    placeholder={Strings.infoDomainsXRoles}
                    choice={this.props.availableRoles}
                    choicePlaceholder={Strings.infoWordPressRole}
                    list={this.reverseKeyValuePairList(this.state.domainsXRoles)}
                    onListChanged={(list: KeyValuePair[]) =>
                      this.setState({
                        domainsXRoles: this.reverseKeyValuePairList(list)
                      })
                    }
                    isValid={new RegExp(/^(?!http)/i)}
                    errorMessage={Strings.errDomainCannotStartWith}>
                    <CalloutButtonWp
                      wpSlug={'domain-roles-mappings'}
                      info={{
                        title: '(Domain) role mappings',
                        readMore: {
                          title: 'Read online',
                          url: 'https://www.wpo365.com/domain-roles-mappings/'
                        }
                      }}
                    />
                  </KeyValueEditorChoice>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[PluginVersion.wpo365LoginPremium, PluginVersion.wpo365LoginIntranet]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>User role(s) update scenario</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Dropdown
                    options={[
                      {
                        key: 'select',
                        text: 'Select',
                        itemType: DropdownMenuItemType.Header
                      },
                      { key: 'add', text: 'Add' },
                      { key: 'replace', text: 'Replace' }
                    ]}
                    selectedKey={this.state.replaceOrUpdateUserRoles}
                    onChange={this.changeReplaceOrUpdateUserRoles}
                  />
                  <CalloutButtonWp
                    wpSlug={'user-roles-update-scenario'}
                    info={{
                      title: 'User role(s) update scenario',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/user-roles-update-scenario/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[PluginVersion.wpo365LoginPremium, PluginVersion.wpo365LoginIntranet]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Default role as fallback</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={this.toggleDefaultRoleAsFallback}
                      checked={this.state.defaultRoleAsFallback}
                    />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={'default-role-as-fallback'}
                    info={{
                      title: 'Default role as fallback',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/default-role-as-fallback/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <PrimaryButton
                  onClick={() => this.saveConfiguration()}
                  iconProps={{
                    iconName: this.state.saveStatus == SaveStatus.success ? 'Like' : 'Dislike'
                  }}
                  style={{ minWidth: '400px', minHeight: '30px' }}
                  disabled={this.state.saveStatus == SaveStatus.saving}>
                  Save configuration
                </PrimaryButton>
                <StyledErrorFor>
                  {errorFor(this.state.errors, 'saveConfiguration').map((error: SettingsException) => (
                    <StyledP>{error.message}</StyledP>
                  ))}
                  {this.state.saveStatus == SaveStatus.success ? Strings.okSettingsUpdated : null}
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        )}
      </StyledTable>
    );
  }

  private errorFor = (fieldName: string): boolean => {
    return false;
  };

  private reverseKeyValuePairList(list: KeyValuePair[]): KeyValuePair[] {
    if (Array.isArray(list)) {
      return list.map((keyValuePair: KeyValuePair) => {
        return {
          key: keyValuePair.value,
          value: keyValuePair.key
        };
      });
    }
    return [];
  }

  private toggleMultiTenanted = (): void => {
    this.setState((prevState: IUserRegistrationTabState, props: IUserRegistrationTabProps) => {
      return { multiTenanted: !prevState.multiTenanted };
    });
  };

  private toggleCreateAndAddUsers = (): void => {
    this.setState((prevState: IUserRegistrationTabState, props: IUserRegistrationTabProps) => {
      return { createAndAddUsers: !prevState.createAndAddUsers };
    });
  };

  private toggleDefaultRoleAsFallback = (): void => {
    this.setState((prevState: IUserRegistrationTabState, props: IUserRegistrationTabProps) => {
      return { defaultRoleAsFallback: !prevState.defaultRoleAsFallback };
    });
  };

  private toggleNewUsrSendMail = (): void => {
    this.setState((prevState: IUserRegistrationTabState, props: IUserRegistrationTabProps) => {
      return { newUsrSendMail: !prevState.newUsrSendMail };
    });
  };

  private toggleNewUsrSendMailAdminOnly = (): void => {
    this.setState((prevState: IUserRegistrationTabState, props: IUserRegistrationTabProps) => {
      const adminOnly = !prevState.newUsrSendMailAdminOnly;
      const useCustom = adminOnly ? false : prevState.newUsrSendMailCustom;
      return {
        newUsrSendMailAdminOnly: adminOnly,
        newUsrSendMailCustom: useCustom
      };
    });
  };

  private toggleNewUsrSendMailCustom = (): void => {
    this.setState((prevState: IUserRegistrationTabState, props: IUserRegistrationTabProps) => {
      const useCustom = !prevState.newUsrSendMailCustom;
      const sendMail = useCustom ? true : prevState.newUsrSendMail;
      const adminOnly = useCustom ? false : prevState.newUsrSendMailAdminOnly;
      return {
        newUsrSendMail: sendMail,
        newUsrSendMailCustom: useCustom,
        newUsrSendMailAdminOnly: adminOnly
      };
    });
  };

  private changeReplaceOrUpdateUserRoles = (event: React.FormEvent<HTMLDivElement>, item: IDropdownOption) => {
    this.setState({ replaceOrUpdateUserRoles: item.key as ReplaceOrAddRoles });
  };

  private changeDefaultRole = (event: React.FormEvent<HTMLDivElement>, item: IDropdownOption) => {
    this.setState({ newUsrDefaultRole: item.key as string });
  };

  private changeMuDefaultRole = (event: React.FormEvent<HTMLDivElement>, item: IDropdownOption) => {
    this.setState({ muNewUsrDefaultRole: item.key as string });
  };

  private toggleBlockEmailChange = (): void => {
    this.setState((prevState: IUserRegistrationTabState, props: IUserRegistrationTabProps) => {
      return { blockEmailChange: !prevState.blockEmailChange };
    });
  };

  private toggleBlockPasswordChange = (): void => {
    this.setState((prevState: IUserRegistrationTabState, props: IUserRegistrationTabProps) => {
      return { blockPasswordChange: !prevState.blockPasswordChange };
    });
  };

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.blockEmailChange = this.state.blockEmailChange;
    settings.blockPasswordChange = this.state.blockPasswordChange;
    settings.createAndAddUsers = this.state.createAndAddUsers;
    settings.customDomain = this.state.customDomain;
    settings.defaultDomain = this.state.defaultDomain;
    settings.defaultRoleAsFallback = this.state.defaultRoleAsFallback;
    settings.domainsXRoles = this.state.domainsXRoles;
    settings.groupsWhitelist = this.state.groupsWhitelist;
    settings.groupsXRoles = this.state.groupsXRoles;
    settings.multiTenanted = this.state.multiTenanted;
    settings.muNewUsrDefaultRole = this.state.muNewUsrDefaultRole;
    settings.newUsrDefaultRole = this.state.newUsrDefaultRole;
    settings.newUsrSendMail = this.state.newUsrSendMail;
    settings.newUsrSendMailAdminOnly = this.state.newUsrSendMailAdminOnly;
    settings.newUsrSendMailCustom = this.state.newUsrSendMailCustom;
    settings.newUsrMailSubject = this.state.newUsrMailSubject;
    settings.newUsrMailTitle = this.state.newUsrMailTitle;
    settings.newUsrMailSalutation = this.state.newUsrMailSalutation;
    settings.newUsrMailBody = this.state.newUsrMailBody;
    settings.newUsrMailFooter = this.state.newUsrMailFooter;
    settings.replaceOrUpdateUserRoles = this.state.replaceOrUpdateUserRoles;
    settings.welcomePageUrl = this.state.welcomePageUrl;

    const updateResult = await Settings.updateSettings(this.props.env, settings);

    if (updateResult.success) {
      this.setState({
        errors: [],
        saveStatus: SaveStatus.success
      });
    } else {
      this.setState((prevState: IUserRegistrationTabState, props: IUserRegistrationTabProps) => {
        return {
          errors: [...prevState.errors, new SettingsException('saveConfiguration', updateResult.message, 2000)],
          saveStatus: SaveStatus.error
        };
      });
    }
  }
}
