import * as React from 'react';

import { PluginVersion } from '../common/Enums';

export interface IVersionSwitchProps {
  pluginVersion: PluginVersion;
  targets: PluginVersion[];
}

export interface IVersionSwitchState {}

export class VersionSwitch extends React.Component<
  IVersionSwitchProps,
  IVersionSwitchState
> {
  public constructor(props: IVersionSwitchProps) {
    super(props);
  }

  public render() {
    if (
      this.props.targets.filter(
        (target: PluginVersion) => target == this.props.pluginVersion
      ).length > 0
    ) {
      return this.props.children;
    } else {
      return null;
    }
  }
}
