import { IToken, TokenRequestError, TokenCache, TokenRequestV2 } from 'pintra-fx';
import Axios from 'axios';

export class SharePointHome {
  public static async get(wpAjaxAdminUrl: string, nonce?: string) {
    const tokenResponse: IToken | TokenRequestError = await TokenCache.getTokenV2(
      new TokenRequestV2('graphSitesReadAll', `https://graph.microsoft.com/Sites.Read.All`),
      {
        wpAjaxAdminUrl,
        nonce
      }
    );

    if (tokenResponse instanceof Error) {
      return new Error('Unexpected: Could not retrieve a valid access token.');
    }

    const token = <IToken>tokenResponse;

    const response = await Axios.get('https://graph.microsoft.com/v1.0/sites/root', {
      headers: {
        Authorization: `Bearer ${token.bearer}`,
        Accept: 'application/json'
      }
    });

    return response;
  }
}
