import * as React from 'react';

export interface IEqualProps<T> {
  a: T;
  b: T;
}

export interface IEqualState {}

export class Equal<T> extends React.Component<IEqualProps<T>, IEqualState> {
  public constructor(props: IEqualProps<T>) {
    super(props);
  }

  public render(): React.ReactElement<IEqualProps<T>> {
    const { a, b, children } = this.props;
    return <React.Fragment>{a === b ? children : null}</React.Fragment>;
  }
}

export class NotEqual<T> extends React.Component<IEqualProps<T>, IEqualState> {
  public constructor(props: IEqualProps<T>) {
    super(props);
  }

  public render(): React.ReactElement<IEqualProps<T>> {
    const { a, b, children } = this.props;
    return <React.Fragment>{a !== b ? children : null}</React.Fragment>;
  }
}
