import * as React from 'react';

import {
  StyledList,
  StyledListRow,
  StyledInputText,
  StyledIconButton,
  StyledRowifier,
  StyledErrorFor,
  StyledP
} from './styled';

import { KeyValuePair } from '../common/KeyValuePair';

import { Icon } from 'office-ui-fabric-react/lib/Icon';
import { IconNames } from '@uifabric/icons';
import { Dropdown, IDropdownOption } from 'office-ui-fabric-react/lib/Dropdown';
import { Strings } from '../strings';
import { SettingsException } from '../common/Exceptions';
import { ValidatableTextInput } from './ValidatableTextInput';
import { Size } from '../common/Enums';

export interface IKeyValueEditorChoiceProps {
  onListChanged: (list: KeyValuePair[]) => void;
  list: KeyValuePair[];
  choice: string[];
  placeholder?: string;
  choicePlaceholder?: string;
  isValid?: RegExp;
  errorMessage?: string;
}

export interface IKeyValueEditorChoiceState {
  list: KeyValuePair[];
  errors: SettingsException[];
  isValid: boolean;
  value: KeyValuePair;
}

export class KeyValueEditorChoice extends React.Component<
  IKeyValueEditorChoiceProps,
  IKeyValueEditorChoiceState
> {
  public constructor(props: IKeyValueEditorChoiceProps) {
    super(props);
    this.state = {
      list: this.props.list,
      errors: [],
      isValid: true,
      value: {
        key: Strings.empty,
        value: Strings.empty
      }
    };
  }

  public componentDidUpdate(prevProps: IKeyValueEditorChoiceProps): void {
    if (this.props.list === prevProps.list) return;
    this.setState({ list: this.props.list });
  }

  public render(): React.ReactElement<IKeyValueEditorChoiceProps> {
    const { choicePlaceholder, choice, isValid, errorMessage } = this.props;
    return (
      <StyledRowifier>
        <StyledList>
          {this.state.list.map((item: KeyValuePair, idx: number) => {
            return (
              <StyledListRow key={idx}>
                <ValidatableTextInput
                  size={Size.xsmall}
                  defaultValue={item.key}
                  value={item.key}
                  bound={true}
                  disabled={true}
                />
                <ValidatableTextInput
                  size={Size.medium}
                  defaultValue={item.value}
                  value={item.value}
                  bound={true}
                  disabled={true}
                />
                <StyledIconButton onClick={() => this.deleteItem(idx)}>
                  <Icon
                    iconName={IconNames.Delete}
                    title={'Delete'}
                    ariaLabel={'Click to delete element'}
                    style={{
                      fontSize: '12px',
                      lineHeight: '30px',
                      height: '30px',
                      width: '30px',
                      margin: '0px 0px 0px -30px',
                      padding: 0,
                      cursor: 'pointer'
                    }}
                  />
                </StyledIconButton>
              </StyledListRow>
            );
          })}
          <StyledListRow>
            <Dropdown
              placeholder={choicePlaceholder}
              options={choice.map((choice: string) => {
                return {
                  key: choice,
                  text: choice
                };
              })}
              onChange={this.onChangeDropDown}
              style={{
                width: '100px'
              }}
            />
            <ValidatableTextInput
              size={Size.medium}
              defaultValue={Strings.empty}
              value={this.state.value.value}
              bound={true}
              isValid={(oldValue: any, newValue: any) => {
                if (
                  isValid &&
                  newValue &&
                  (newValue as string) != Strings.empty
                )
                  return isValid.test(newValue as string);
                return true;
              }}
              onValid={this.onValid}
              onError={this.onError}
              errorMessage={errorMessage}
            />
            {/* <StyledInputText ref={this.newItemValue} style={{ width: '100px', minWidth: '100px' }} /> */}
            <StyledIconButton onClick={this.addItem}>
              <Icon
                iconName={IconNames.Add}
                title={'Add'}
                ariaLabel={'Click to add this item'}
                style={{
                  fontSize: '12px',
                  lineHeight: '30px',
                  height: '30px',
                  width: '30px',
                  margin: '0px 0px 0px -30px',
                  padding: 0,
                  cursor: 'pointer'
                }}
              />
            </StyledIconButton>
          </StyledListRow>
        </StyledList>
        {this.props.children}
        <StyledErrorFor>
          {this.state.errors.map((error: SettingsException) => (
            <StyledP>{error.message}</StyledP>
          ))}
        </StyledErrorFor>
      </StyledRowifier>
    );
  }

  private onChangeDropDown = (
    event: React.FormEvent<HTMLDivElement>,
    option: IDropdownOption
  ) => {
    this.setState((prevState: IKeyValueEditorChoiceState) => ({
      value: {
        key: option.key as string,
        value: prevState.value.value
      }
    }));
  };

  private onError = (errorMessage: string) => {
    this.setState({
      errors: [new SettingsException(null, errorMessage)],
      isValid: false
    });
  };

  private onValid = (newValue: any) => {
    this.setState((prevState: IKeyValueEditorChoiceState) => ({
      value: {
        key: prevState.value.key,
        value: newValue as string
      },
      errors: [],
      isValid: true
    }));
  };

  private addItem = () => {
    const { list } = this.state;
    if (
      this.state.isValid &&
      this.state.value.key != Strings.empty &&
      this.state.value.value != Strings.empty
    ) {
      const newList = [...list, this.state.value];
      this.setState({
        list: newList,
        value: {
          key: '',
          value: ''
        }
      });
      this.props.onListChanged(newList);
    }
  };

  private deleteItem = (idx: number) => {
    const newList = [...this.state.list];
    newList.splice(idx, 1);
    this.setState({ list: newList });
    this.props.onListChanged(newList);
  };
}
