import * as React from 'react';

import {
  StyledIconButton,
  StyledCallout,
  StyledCalloutTitle,
  StyledCalloutContent,
  StyledCalloutFooter
} from './styled';

import { Icon } from 'office-ui-fabric-react/lib/Icon';
import { IconNames } from '@uifabric/icons';
import { Callout } from 'office-ui-fabric-react/lib/Callout';

import { getRenderedPost } from '../common/Helpers';

export interface ReadMore {
  title: string;
  url: string;
}

export interface Info {
  title: string;
  readMore: ReadMore;
}

export interface ICalloutButtonWpProps {
  wpSlug: string;
  info?: Info;
}

export interface ICalloutButtonWpState {
  calloutVisible: boolean;
  html: string;
}

export class CalloutButtonWp extends React.Component<
  ICalloutButtonWpProps,
  ICalloutButtonWpState
> {
  private infoButtonRef = React.createRef<any>();

  public constructor(props: ICalloutButtonWpProps) {
    super(props);
    this.state = {
      calloutVisible: false,
      html: ''
    };
  }

  public componentDidMount(): void {
    getRenderedPost(this.props.wpSlug).then((html: string | Error) => {
      if (html instanceof Error) {
        console.log(html.message);
        return;
      }
      this.setState({ html });
    });
  }

  public render(): React.ReactElement<ICalloutButtonWpProps> {
    return (
      <div>
        <StyledIconButton
          onClick={() => this.setState({ calloutVisible: true })}
          ref={this.infoButtonRef}
        >
          <Icon
            iconName={IconNames.Info}
            title={'Click for instant help'}
            ariaLabel={'Click for instant help'}
            style={{
              fontSize: '12px',
              lineHeight: '30px',
              height: '30px',
              width: '30px',
              margin: '0px 0px 0px 15px',
              padding: 0,
              cursor: 'pointer'
            }}
          />
        </StyledIconButton>
        {this.props.info ? (
          <Callout
            role={'alertdialog'}
            gapSpace={0}
            target={this.infoButtonRef.current}
            onDismiss={() => this.setState({ calloutVisible: false })}
            setInitialFocus={true}
            hidden={!this.state.calloutVisible}
          >
            <StyledCallout>
              <StyledCalloutTitle>{this.props.info.title}</StyledCalloutTitle>
              <StyledCalloutContent>
                <div dangerouslySetInnerHTML={{ __html: this.state.html }} />
              </StyledCalloutContent>
              <StyledCalloutFooter>
                <a href={this.props.info.readMore.url} target={'_blank'}>
                  {this.props.info.readMore.title}
                </a>
              </StyledCalloutFooter>
            </StyledCallout>
          </Callout>
        ) : null}
      </div>
    );
  }
}
