import * as React from 'react';

import { IEnv } from './Wizard';

import { Settings } from '../common/Settings';

import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledRowifier,
  StyledSettings,
  StyledCheckbox,
  StyledErrorFor,
  StyledP,
  StyledLogItemCell,
  StyledLogItemTimeStamp,
  StyledLogItemType,
  StyledLogItemMessage,
  StyledListWrapper,
  StyledListMenu,
  StyledListMain
} from './styled';

import { Footer } from './Footer';

import { DefaultButton, PrimaryButton, CommandBarButton, IButtonStyles } from 'office-ui-fabric-react/lib/Button';
import { List } from 'office-ui-fabric-react/lib/List';
import { IOverflowSetItemProps, OverflowSet } from 'office-ui-fabric-react/lib/OverflowSet';

import { Strings } from '../strings';
import { SaveStatus, LogItemType } from '../common/Enums';
import { SettingsException } from '../common/Exceptions';
import { Checkbox } from 'office-ui-fabric-react/lib/Checkbox';
import { CalloutButtonWp } from './CalloutButtonWp';
import { errorFor } from '../common/Helpers';
import { Log } from '../common/Log';

export interface IDebugTabProps {
  env: IEnv;
  settings: Settings;
  getSettings: () => void;
}

export interface IDebugTabState {
  debugLog: boolean;
  debugLogIdToken: boolean;
  hideErrorNotice: boolean;
  log: string;
  parsedLog: ILogItem[];
  filteredLog: ILogItem[];
  // ---
  copyButtonCaption: string;
  copying: boolean;
  errors: SettingsException[];
  saveStatus: SaveStatus;
}

export interface ILogItem {
  timestamp: string;
  phpVersion: string;
  type?: LogItemType;
  message: string;
}

const buttonStyles: Partial<IButtonStyles> = {
  root: {
    minWidth: 0,
    padding: '10px',
    alignSelf: 'stretch',
    height: 'auto'
  }
};

export class DebugTab extends React.Component<IDebugTabProps, IDebugTabState> {
  private logTextArea: any;

  public constructor(props: IDebugTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      debugLog: settings.debugLog,
      debugLogIdToken: settings.debugLogIdToken,
      hideErrorNotice: settings.hideErrorNotice,
      log: '',
      parsedLog: [],
      filteredLog: [],
      copyButtonCaption: Strings.capCopyLogButton,
      copying: false,
      errors: [],
      saveStatus: SaveStatus.initial
    };
  }

  public componentDidMount(): void {
    const { env } = this.props;
    Log.getLog(env)
      .then((log: string) => {
        const parsedLog = this.parseLog(log as any);
        this.setState({ log, parsedLog });
        this.filterLog((logItem: ILogItem) => logItem.type === LogItemType.ERROR);
      })
      .catch((error: any) => alert(error.meesage));
  }

  public componentDidUpdate(prevProps: IDebugTabProps): void {
    const { settings } = this.props;
    if (prevProps.settings != settings) {
      this.setState({
        debugLog: settings.debugLog,
        debugLogIdToken: settings.debugLogIdToken,
        hideErrorNotice: settings.hideErrorNotice
      });
    }
  }

  public render(): React.ReactElement<IDebugTabProps> {
    const { debugLog, debugLogIdToken, copyButtonCaption, log, parsedLog, filteredLog, copying } = this.state;
    return (
      <React.Fragment>
        <StyledTable>
          <StyledTableCaption>
            <StyledTableHeader>{Strings.titleDebug}</StyledTableHeader>
            <StyledTableIntro
              dangerouslySetInnerHTML={{
                __html: `<strong>Use this tab to</strong>
                <ul>
                <li>Enable / Disable the plugin's debug log</li>
                <li>Enable / Disable logging of the Open ID Connect information (ID Token)</li>
                <li>Copy the debug log to your clipboard e.g. to send it to WPO365 support when requested to do so</li>
                </ul>
                <strong>Important</strong><br/><br/>
                Disable logging if the plugin is working as expected, as it will require additional resources e.g. 
                database requests and I/O operations and unnecessarily impact your web application's performance.
                `
              }}
            />
          </StyledTableCaption>
          <StyledTableRowGroup>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Enable debug log</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox onChange={this.toggleDebugLog} checked={this.state.debugLog} />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={'debug-log'}
                    info={{
                      title: 'Debug log',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/debug-log/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Hide error notice</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox onChange={this.toggleHideErrorNotice} checked={this.state.hideErrorNotice} />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={'hide-error-notice'}
                    info={{
                      title: 'Hide error notice',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/hide-error-notice/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Log ID token</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox onChange={this.toggleDebugLogIdToken} checked={this.state.debugLogIdToken} />
                  </StyledCheckbox>
                  <CalloutButtonWp
                    wpSlug={'log-id-token'}
                    info={{
                      title: 'Log ID token',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/log-id-token/'
                      }
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell />
              <StyledTableCell>
                <StyledRowifier>
                  <PrimaryButton
                    onClick={() => this.saveConfiguration()}
                    iconProps={{
                      iconName: this.state.saveStatus == SaveStatus.success ? 'Like' : 'Dislike'
                    }}
                    style={{ minWidth: '400px', minHeight: '30px' }}
                    disabled={this.state.saveStatus == SaveStatus.saving}>
                    Save configuration
                  </PrimaryButton>
                  <StyledErrorFor>
                    {errorFor(this.state.errors, 'saveConfiguration').map((error: SettingsException) => (
                      <StyledP>{error.message}</StyledP>
                    ))}
                    {this.state.saveStatus == SaveStatus.success ? Strings.okSettingsUpdated : null}
                  </StyledErrorFor>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>&nbsp;</StyledTableCell>
              <StyledTableCell>&nbsp;</StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Debug log:</StyledLabel>
              </StyledTableCell>
              <StyledTableCell style={{ maxWidth: '700px' }}>
                <StyledListWrapper>
                  <StyledListMenu>
                    <OverflowSet
                      items={[
                        {
                          key: 'filterErrors',
                          name: 'Errors only',
                          icon: 'Filter',
                          ariaLabel: 'Filter: Errors',
                          onClick: () => this.filterLog((logItem: ILogItem) => logItem.type === LogItemType.ERROR)
                        },
                        {
                          key: 'filterDebug',
                          name: 'Debug only',
                          icon: 'Filter',
                          ariaLabel: 'Filter: Debug',
                          onClick: () => this.filterLog((logItem: ILogItem) => logItem.type === LogItemType.DEBUG)
                        },
                        {
                          key: 'deleteFilter',
                          name: 'Show all',
                          icon: 'ClearFilter',
                          ariaLabel: 'Delete filters',
                          onClick: () => this.setState({ filteredLog: parsedLog })
                        },
                        {
                          key: 'copyClipboard',
                          name: 'Copy to clipboard',
                          icon: 'Copy',
                          ariaLabel: 'Copy to clipboard',
                          onClick: this.copyToClipboard
                        }
                      ]}
                      overflowItems={[]}
                      onRenderOverflowButton={this.onRenderOverflowButton}
                      onRenderItem={this.onRenderItem}
                    />
                  </StyledListMenu>
                  <StyledListMain>
                    <List items={filteredLog} onRenderCell={this.debugLogListCell} />
                  </StyledListMain>
                </StyledListWrapper>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow style={{ display: copying ? 'inherit' : 'none' }}>
              <StyledTableCell>
                <StyledLabel>Full debug log (for copying):</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledSettings
                  ref={(textarea: any) => (this.logTextArea = textarea)}
                  readOnly={true}
                  value={JSON.stringify(parsedLog, null, 2)}
                />
              </StyledTableCell>
            </StyledTableRow>
          </StyledTableRowGroup>
          <Footer />
        </StyledTable>
      </React.Fragment>
    );
  }

  private debugLogListCell(item: ILogItem, index: number | undefined): JSX.Element {
    return (
      <StyledLogItemCell data-is-focusable={true}>
        <StyledLogItemTimeStamp dangerouslySetInnerHTML={{ __html: item.timestamp }} />
        <StyledLogItemType dangerouslySetInnerHTML={{ __html: item.type }} />
        <StyledLogItemMessage dangerouslySetInnerHTML={{ __html: item.message }} />
      </StyledLogItemCell>
    );
  }

  private onRenderItem = (item: IOverflowSetItemProps): JSX.Element => {
    if (item.onRender) {
      return item.onRender(item);
    }
    return (
      <CommandBarButton
        iconProps={{ iconName: item.icon }}
        menuProps={item.subMenuProps}
        text={item.name}
        onClick={item.onClick}
        styles={buttonStyles}
      />
    );
  };

  private onRenderOverflowButton = (overflowItems: any[] | undefined): JSX.Element => {
    return (
      <CommandBarButton
        styles={buttonStyles}
        menuIconProps={{ iconName: 'More' }}
        menuProps={{ items: overflowItems! }}
      />
    );
  };

  private parseLog = (log: string[]): ILogItem[] => {
    const parsedLog: ILogItem[] = [];
    for (let i = 0; i < log.length; i++) {
      let logEntry = log[i];
      // Timestamp
      const timeStampLen = logEntry.indexOf(']') + 1;
      const timestamp = logEntry.substr(0, timeStampLen);
      logEntry = logEntry.substr(timeStampLen + 1);
      // Type
      const type = LogItemType[logEntry.substr(0, 5) as any] as LogItemType;
      logEntry = logEntry.substr(5);
      // PHP Version
      const phpVersionStart = logEntry.indexOf('(') + 2;
      const phpVersionEnd = logEntry.indexOf(')') - 2;
      const phpVersion = logEntry.substr(phpVersionStart, phpVersionEnd - phpVersionStart);
      const message = logEntry.substr(phpVersionEnd + 5);
      parsedLog.push({
        timestamp,
        type,
        phpVersion,
        message
      });
    }

    return parsedLog;
  };

  private filterLog = (filter: (logItem: ILogItem) => boolean) => {
    const { parsedLog } = this.state;
    const filteredLog = parsedLog.filter(filter);
    if (filteredLog.length == 0) {
      filteredLog.push({
        timestamp: '',
        phpVersion: '',
        message: '<strong>No result</strong> <i>Try another filter...</i>'
      });
    }
    this.setState({ filteredLog });
  };

  private toggleDebugLog = () => {
    this.setState((prevState: IDebugTabState, props: IDebugTabProps) => {
      return { debugLog: !prevState.debugLog };
    });
  };

  private toggleHideErrorNotice = () => {
    this.setState((prevState: IDebugTabState, props: IDebugTabProps) => {
      return { hideErrorNotice: !prevState.hideErrorNotice };
    });
  };

  private toggleDebugLogIdToken = (): void => {
    this.setState((prevState: IDebugTabState, props: IDebugTabProps) => {
      return { debugLogIdToken: !prevState.debugLogIdToken };
    });
  };

  // Copies the settings to the clipboard and shows a message during 5 seconds

  private copyToClipboard = (event: React.MouseEvent<HTMLButtonElement>): void => {
    this.setState({ copying: true }, () => {
      this.logTextArea.select();
      document.execCommand('copy');
      this.setState({
        copyButtonCaption: Strings.capCopyLogButtonSuccess,
        copying: false
      });
      setTimeout(() => this.setState({ copyButtonCaption: Strings.capCopyLogButton }), 3000);
    });
  };

  // Save settings

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.debugLog = this.state.debugLog;
    settings.debugLogIdToken = this.state.debugLogIdToken;
    settings.hideErrorNotice = this.state.hideErrorNotice;
    const updateResult = await Settings.updateSettings(this.props.env, settings);

    if (updateResult.success) {
      this.setState({
        errors: [],
        saveStatus: SaveStatus.success
      });
    } else {
      this.setState((prevState: IDebugTabState, props: IDebugTabProps) => {
        return {
          errors: [...prevState.errors, new SettingsException('saveConfiguration', updateResult.message, 2000)],
          saveStatus: SaveStatus.error
        };
      });
    }
  }
}
