import * as React from 'react';

import {
  StyledTableCaption,
  StyledTable,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledRowifier,
  StyledCheckbox,
  StyledTextArea
} from './styled';

import { IEnv } from './Wizard';
import { ValidatableTextInput } from './ValidatableTextInput';
import { Strings } from '../strings';
import { Footer } from './Footer';

import { Checkbox } from 'office-ui-fabric-react/lib/Checkbox';
import { DefaultButton } from 'office-ui-fabric-react/lib/Button';
import { IAdditionalInfoDialogProps } from '../common/IAdditionalInfoDialogProps';
import { AdditionalInfoDialog } from './AdditionalInfoDialog';
import { Size, PluginVersion } from '../common/Enums';

export interface IDocsConfiguratorProps {
  env: IEnv;
}

export interface IDocsConfiguratorState {
  scriptSrc: string;
  pageSize: number;
  hostname: string;
  serverRelativePath: string;
  listTitle: string;
  hideMessageBar: boolean;
  copyButtonCaption: string;
}

export class DocsConfigurator extends React.Component<IDocsConfiguratorProps, IDocsConfiguratorState> {
  public constructor(props: IDocsConfiguratorProps) {
    super(props);
    const { env } = this.props;
    this.state = {
      scriptSrc: this.currentScriptSrc('wizard.js').replace(
        'wizard.js',
        env.props.pluginVersion === PluginVersion.wpo365LoginIntranet ? 'docs.js' : 'docsBasic.js'
      ),
      pageSize: 10,
      hostname: '',
      serverRelativePath: '',
      listTitle: '',
      hideMessageBar: false,
      // ----- //
      copyButtonCaption: Strings.capCopyShortcodeButton
    };
  }

  public render(): React.ReactElement<IDocsConfiguratorProps> {
    const { env } = this.props;
    const { copyButtonCaption } = this.state;
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>Documents (SharePoint Online / OneDrive) shortcode generator</StyledTableHeader>
          <StyledTableIntro>
            To create the shortcode you can use this generator. Simply configure the options and copy the shortcode
            to the clipboard. Then all you need to do is to add the shortcode to a page and you're ready!
            <br />
            <br />
            Please consult the online documentation{' '}
            <a href={'https://www.wpo365.com/documents/'}>https://www.wpo365.com/documents/</a> for instructions on
            how to generate a shortcode and place it on any WordPress post or page.
          </StyledTableIntro>
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>Page size</StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                bound={false}
                isValid={(oldValue: any, newValue: any) => {
                  const newValueAsInt = parseInt(newValue);
                  return (
                    (newValue as string) === newValueAsInt.toString() && newValueAsInt > 0 && newValueAsInt < 50
                  );
                }}
                onValid={(newValue: any) => this.setState({ pageSize: parseInt(newValue) })}
                errorMessage={'pageSize must be number greater than 0 and smaller than 50'}
                defaultValue={'10'}
              />
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>Hostname</StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                bound={false}
                placeholder={'e.g. your-tenant.sharepoint.com'}
                isValid={(oldValue: any, newValue: any) => !/^http|\/$/.test(newValue)}
                onValid={(newValue: any) => this.setState({ hostname: newValue })}
                errorMessage={'Hostname can not start with http or end with /'}
                defaultValue={''}
              />
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>Server relative library path</StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                bound={false}
                placeholder={'e.g. sites/your-site'}
                isValid={(oldValue: any, newValue: any) => !/^\/|\/$/.test(newValue)}
                onValid={(newValue: any) => this.setState({ serverRelativePath: newValue })}
                errorMessage={'Server relative path can not start or end with /'}
                defaultValue={''}
              />
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>List title</StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                bound={false}
                placeholder={'e.g. Documents'}
                isValid={(oldValue: any, newValue: any) => !/^\/|\/$/.test(newValue)}
                onValid={(newValue: any) => this.setState({ listTitle: newValue })}
                errorMessage={'List title cannot start or end with /'}
                defaultValue={''}
              />
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>Hide message bar</StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <Checkbox
                  checked={this.state.hideMessageBar}
                  onChange={(event: React.MouseEvent<HTMLElement>) =>
                    this.setState((prevState: IDocsConfiguratorState) => {
                      return { hideMessageBar: !prevState.hideMessageBar };
                    })
                  }
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>Shortcode</StyledTableCell>
            <StyledTableCell>
              <StyledTextArea
                readOnly={true}
                value={`[pintra props="pageSize,${this.state.pageSize};hostname,${
                  this.state.hostname
                };serverRelativePath,${this.state.serverRelativePath};listTitle,${
                  this.state.listTitle
                };hideMessageBar,${this.state.hideMessageBar ? 'true' : 'false'}" script_url="${
                  this.state.scriptSrc
                }"]`}
                className={'pintraSpoShortCode'}
              />
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <DefaultButton
                  onClick={this.copyShortcode}
                  iconProps={{ iconName: 'Copy' }}
                  style={{ minWidth: '400px', minHeight: '30px' }}>
                  {copyButtonCaption}
                </DefaultButton>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {/* this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        ) */}
      </StyledTable>
    );
  }

  private copyShortcode = () => {
    (document.getElementsByClassName('pintraSpoShortCode')[0] as HTMLTextAreaElement).select();
    document.execCommand('copy');
    this.setState({
      copyButtonCaption: Strings.capCopyShortcodeButtonSuccess
    });
    setTimeout(() => this.setState({ copyButtonCaption: Strings.capCopyShortcodeButton }), 3000);
  };

  private currentScriptSrc = (scriptName: string): string | null => {
    const scriptTags = document.getElementsByTagName('script');
    for (let i = scriptTags.length - 1; i >= 0; i--) {
      if (scriptTags[i].src.indexOf(scriptName) > -1) {
        return scriptTags[i].src;
      }
    }
    return null;
  };
}
