import * as React from 'react';

import { Pivot, PivotItem, IPivotItemProps, IPivot } from 'office-ui-fabric-react/lib/Pivot';
import { IOverflowSetItemProps, OverflowSet } from 'office-ui-fabric-react/lib/OverflowSet';
import { IButtonStyles, IconButton, CommandBarButton } from 'office-ui-fabric-react/lib/Button';
import { Link } from 'office-ui-fabric-react/lib/Link';

import { AuthenticationTab } from './AuthenticationTab';
import { UserRegistrationTab } from './UserRegistrationTab';
import { IntegrationTab } from './IntegrationTab';
import { Settings } from '../common/Settings';

import { initializeIcons } from 'office-ui-fabric-react/lib/Icons';
import { PluginVersion } from '../common/Enums';
import { ErrorsTab } from './ErrorsTab';
import { MiscellaneousTab } from './MiscellaneousTab';
import { DownloadsTab } from './DownloadsTab';
import { UserSyncTab } from './UserSyncTab';
import { LoginLogoutBehavourTab } from './LoginLogoutBehaviourTab';
import { InstallationTab } from './InstallationTab';
import { ExportImportTab } from './ExportImportTab';
import { DebugTab } from './DebugTab';
import { CbsConfigurator } from './cbsConfigurator';
import { IRefObject } from '@uifabric/utilities';
import { GlobalWizardStyled } from './styled';
import { EdConfigurator } from './edConfigurator';
import { RecentConfigurator } from './recentConfigurator';
import { DocsConfigurator } from './docsConfigurator';

initializeIcons(/* optional base url */);

export interface IEnv {
  nonce: string;
  wpAjaxAdminUrl: string;
  props: IEnvProps;
}

export interface IEnvProps {
  siteUrl: string;
  adminUrl: string;
  nonce: string;
  pluginVersion: PluginVersion;
  availableRoles: string;
}

export interface IWizardProps {
  env: IEnv;
}

export interface IWizardState {
  settings: Settings;
  pivotItems: React.ReactElement<IPivotItemProps>[];
  pivotSelectedKey: string | null;
  visibleMenuItems: IOverflowSetItemProps[];
  invisbleMenuItems: IOverflowSetItemProps[];
}

export class Wizard extends React.Component<IWizardProps, IWizardState> {
  public constructor(props: IWizardProps) {
    super(props);
    this.state = {
      settings: new Settings(this.props.env),
      pivotItems: [],
      pivotSelectedKey: '',
      visibleMenuItems: [],
      invisbleMenuItems: []
    };
    this.getSettings(() => {
      this.setState({
        settings: new Settings(this.props.env),
        pivotItems: this.getPivotItems(),
        pivotSelectedKey: 'installationGuide',
        visibleMenuItems: this.getVisibleMenuItems(),
        invisbleMenuItems: this.getInvisibleMenuItems()
      });
    });
  }

  public render(): React.ReactElement<IWizardProps> {
    const { pivotItems, visibleMenuItems, invisbleMenuItems, pivotSelectedKey } = this.state;
    return (
      <React.Fragment>
        <GlobalWizardStyled />
        <OverflowSet
          items={visibleMenuItems}
          overflowItems={invisbleMenuItems}
          onRenderOverflowButton={this.onRenderOverflowButton}
          onRenderItem={this.onRenderOverflowItem}
          className={'pintraOverflowMenu'}
        />
        <Pivot selectedKey={pivotSelectedKey}>{pivotItems}</Pivot>
      </React.Fragment>
    );
  }

  // Update the settings

  private getSettings = (callback?: () => void): void => {
    const { env } = this.props;
    Settings.getSettings(env).then((settings: Settings) => {
      this.setState({ settings }, () => (callback ? callback() : false));
    });
  };

  // Compile the pivot

  private getPivotItems = (): React.ReactElement<IPivotItemProps>[] => {
    const pivotItems: React.ReactElement<IPivotItemProps>[] = [];

    pivotItems.push(
      <PivotItem
        itemKey={'installationGuide'}
        headerText={'Installation Guide'}
        headerButtonProps={{
          'data-order': 0,
          'data-title': 'Installation Guide'
        }}>
        <InstallationTab env={this.props.env} />
      </PivotItem>
    );

    if (
      [
        PluginVersion.wpo365LoginProfessional,
        PluginVersion.wpo365LoginPremium,
        PluginVersion.wpo365LoginIntranet
      ].filter((target: PluginVersion) => target == this.props.env.props.pluginVersion).length > 0
    ) {
      pivotItems.push(
        <PivotItem
          itemKey={'license'}
          headerText={'Downloads'}
          headerButtonProps={{
            'data-order': 1,
            'data-title': 'Downloads'
          }}>
          <DownloadsTab env={this.props.env} settings={this.state.settings} />
        </PivotItem>
      );
    }

    pivotItems.push(
      <PivotItem
        itemKey={'singleSignOn'}
        headerText={'Single Sign-on'}
        headerButtonProps={{
          'data-order': 2,
          'data-title': 'Single Sign-on'
        }}>
        <AuthenticationTab env={this.props.env} settings={this.state.settings} />
      </PivotItem>
    );

    pivotItems.push(
      <PivotItem
        itemKey={'integration'}
        headerText={'Integration'}
        headerButtonProps={{
          'data-order': 3,
          'data-title': 'Integration'
        }}>
        <IntegrationTab settings={this.state.settings} env={this.props.env} />
      </PivotItem>
    );

    pivotItems.push(
      <PivotItem
        itemKey={'userRegistration'}
        headerText={'User Registration'}
        headerButtonProps={{
          'data-order': 4,
          'data-title': 'User Registration'
        }}>
        <UserRegistrationTab
          env={this.props.env}
          settings={this.state.settings}
          availableRoles={Object.keys(JSON.parse(this.props.env.props.availableRoles))}
        />
      </PivotItem>
    );

    if (
      [
        PluginVersion.wpo365LoginProfessional,
        PluginVersion.wpo365LoginPremium,
        PluginVersion.wpo365LoginIntranet
      ].filter((target: PluginVersion) => target == this.props.env.props.pluginVersion).length > 0
    ) {
      pivotItems.push(
        <PivotItem
          itemKey={'loginLogout'}
          headerText={'Login / Logout'}
          headerButtonProps={{
            'data-order': 5,
            'data-title': 'Login / Logout behaviour'
          }}>
          <LoginLogoutBehavourTab env={this.props.env} settings={this.state.settings} />
        </PivotItem>
      );
    }

    if (
      [PluginVersion.wpo365LoginPremium, PluginVersion.wpo365LoginIntranet].filter(
        (target: PluginVersion) => target == this.props.env.props.pluginVersion
      ).length > 0
    ) {
      pivotItems.push(
        <PivotItem
          itemKey={'userSync'}
          headerText={'User Sync'}
          headerButtonProps={{
            'data-order': 6,
            'data-title': 'User Synchronization'
          }}>
          <UserSyncTab env={this.props.env} settings={this.state.settings} />
        </PivotItem>
      );
    }

    pivotItems.push(
      <PivotItem
        itemKey={'errors'}
        headerText={'Errors'}
        headerButtonProps={{
          'data-order': 7,
          'data-title': 'Errors'
        }}>
        <ErrorsTab settings={this.state.settings} env={this.props.env} />
      </PivotItem>
    );

    pivotItems.push(
      <PivotItem
        itemKey={'miscellaneous'}
        headerText={'Miscellaneous'}
        headerButtonProps={{
          'data-order': 8,
          'data-title': 'Miscellaneous'
        }}>
        <MiscellaneousTab settings={this.state.settings} env={this.props.env} />
      </PivotItem>
    );

    pivotItems.push(
      <PivotItem
        itemKey={'debug'}
        headerText={'Debug'}
        headerButtonProps={{
          'data-order': 9,
          'data-title': 'Debug'
        }}>
        <DebugTab settings={this.state.settings} env={this.props.env} getSettings={this.getSettings} />
      </PivotItem>
    );

    pivotItems.push(
      <PivotItem
        itemKey={'importExport'}
        headerText={'Import / Export'}
        headerButtonProps={{
          'data-order': 10,
          'data-title': 'Import / Export'
        }}>
        <ExportImportTab settings={this.state.settings} env={this.props.env} getSettings={this.getSettings} />
      </PivotItem>
    );

    pivotItems.push(
      <PivotItem
        itemKey={'cbsConfigurator'}
        headerText={'Content by Search (SharePoint)'}
        headerButtonProps={{
          'data-order': 11,
          'data-title': 'Content by Search (SharePoint)'
        }}>
        <CbsConfigurator env={this.props.env} />
      </PivotItem>
    );

    pivotItems.push(
      <PivotItem
        itemKey={'docsConfigurator'}
        headerText={'Documents (SharePoint Online / OneDrive)'}
        headerButtonProps={{
          'data-order': 12,
          'data-title': 'Documents (SharePoint Online / OneDrive)'
        }}>
        <DocsConfigurator env={this.props.env} />
      </PivotItem>
    );

    pivotItems.push(
      <PivotItem
        itemKey={'edConfigurator'}
        headerText={'Employee Directory (Microsoft Graph)'}
        headerButtonProps={{
          'data-order': 13,
          'data-title': 'Employee Directory (Microsoft Graph)'
        }}>
        <EdConfigurator env={this.props.env} />
      </PivotItem>
    );

    /* pivotItems.push(
      <PivotItem
        itemKey={'recentConfigurator'}
        headerText={'Recent Documents (Microsoft Graph)'}
        headerButtonProps={{
          'data-order': 12,
          'data-title': 'Recent Documents (Microsoft Graph)'
        }}>
        <RecentConfigurator env={this.props.env} />
      </PivotItem>
    ); */

    return pivotItems;
  };

  // Compile the visible Overflow Set items

  private getVisibleMenuItems = (): IOverflowSetItemProps[] => {
    const visibleMenuItems: IOverflowSetItemProps[] = [];

    visibleMenuItems.push({
      key: 'installationGuide',
      name: 'Installation Guide',
      onClick: () => this.activateTab('installationGuide')
    });

    visibleMenuItems.push({
      key: 'singleSignOn',
      name: 'Single Sign-on',
      onClick: () => this.activateTab('singleSignOn')
    });

    visibleMenuItems.push({
      key: 'userRegistration',
      name: 'User Registration',
      onClick: () => this.activateTab('userRegistration')
    });

    if (
      [
        PluginVersion.wpo365LoginProfessional,
        PluginVersion.wpo365LoginPremium,
        PluginVersion.wpo365LoginIntranet
      ].filter((target: PluginVersion) => target == this.props.env.props.pluginVersion).length > 0
    ) {
      visibleMenuItems.push({
        key: 'loginLogout',
        name: 'Login / Logout',
        onClick: () => this.activateTab('loginLogout')
      });
    }

    if (
      [PluginVersion.wpo365LoginPremium, PluginVersion.wpo365LoginIntranet].filter(
        (target: PluginVersion) => target == this.props.env.props.pluginVersion
      ).length > 0
    ) {
      visibleMenuItems.push({
        key: 'userSync',
        name: 'User Sync',
        onClick: () => this.activateTab('userSync')
      });
    }

    visibleMenuItems.push({
      key: 'integration',
      name: 'Integration',
      onClick: () => this.activateTab('integration')
    });

    return visibleMenuItems;
  };

  private getInvisibleMenuItems = (): IOverflowSetItemProps[] => {
    const invisibleMenuItems: IOverflowSetItemProps[] = [];

    if (
      [
        PluginVersion.wpo365LoginProfessional,
        PluginVersion.wpo365LoginPremium,
        PluginVersion.wpo365LoginIntranet
      ].filter((target: PluginVersion) => target == this.props.env.props.pluginVersion).length > 0
    ) {
      invisibleMenuItems.push({
        key: 'license',
        name: 'License',
        onClick: () => this.activateTab('license')
      });
    }

    invisibleMenuItems.push({
      key: 'errors',
      name: 'Custom Errors',
      onClick: () => this.activateTab('errors')
    });

    invisibleMenuItems.push({
      key: 'debug',
      name: 'Debug',
      onClick: () => this.activateTab('debug')
    });

    invisibleMenuItems.push({
      key: 'importExport',
      name: 'Import / Export',
      onClick: () => this.activateTab('importExport')
    });

    invisibleMenuItems.push({
      key: 'cbsConfigurator',
      name: 'Content by Search (SharePoint)',
      onClick: () => this.activateTab('cbsConfigurator')
    });

    invisibleMenuItems.push({
      key: 'docsConfigurator',
      name: 'Documents (SharePoint / OneDrive)',
      onClick: () => this.activateTab('docsConfigurator')
    });

    invisibleMenuItems.push({
      key: 'edConfigurator',
      name: 'Employee Directory (Graph)',
      onClick: () => this.activateTab('edConfigurator')
    });

    /* invisibleMenuItems.push({
      key: 'recentConfigurator',
      name: 'Recent Documents (Graph)',
      onClick: () => this.activateTab('recentConfigurator')
    }); */

    invisibleMenuItems.push({
      key: 'miscellaneous',
      name: 'Miscellaneous',
      onClick: () => this.activateTab('miscellaneous')
    });

    return invisibleMenuItems;
  };

  private activateTab = (pivotSelectedKey: string): void => {
    this.setState({ pivotSelectedKey });
  };

  private onRenderOverflowItem = (item: IOverflowSetItemProps): JSX.Element => {
    if (item.onRender) {
      return item.onRender(item);
    }
    return (
      <CommandBarButton
        iconProps={{ iconName: item.icon }}
        menuProps={item.subMenuProps}
        text={item.name}
        onClick={item.onClick}
      />
    );
  };

  private onRenderOverflowButton = (overflowItems: any[] | undefined): JSX.Element => {
    const buttonStyles: Partial<IButtonStyles> = {
      root: {
        minWidth: 0,
        padding: '0 4px',
        alignSelf: 'stretch',
        height: 'auto'
      }
    };
    return (
      <CommandBarButton
        styles={buttonStyles}
        menuIconProps={{ iconName: 'More' }}
        menuProps={{ items: overflowItems! }}
      />
    );
  };
}
