import * as React from 'react';

import { PrimaryButton, DefaultButton } from 'office-ui-fabric-react/lib/Button';
import { Dialog, DialogType, DialogFooter } from 'office-ui-fabric-react/lib/Dialog';

import { Strings } from '../strings';

import { IAdditionalInfoDialogProps } from '../common/IAdditionalInfoDialogProps';

export const AdditionalInfoDialog: React.FunctionComponent<IAdditionalInfoDialogProps> = (
  props: IAdditionalInfoDialogProps
) => {
  const { onDismiss, onCancel, onConfirm, title, subText } = props;
  return (
    <React.Fragment>
      <Dialog
        hidden={false}
        onDismiss={onDismiss}
        dialogContentProps={{
          type: DialogType.normal,
          title,
          subText: ''
        }}
        modalProps={{
          isBlocking: false
        }}>
        <div dangerouslySetInnerHTML={{ __html: subText }} />
        <DialogFooter>
          {!!onCancel && <DefaultButton onClick={onCancel} text={Strings.capCancelButton} />}
          <PrimaryButton onClick={onConfirm} text={Strings.capConfirmButton} />
        </DialogFooter>
      </Dialog>
    </React.Fragment>
  );
};
