import * as React from 'react';

import Axios from 'axios';

import {
  StyledTableCaption,
  StyledTable,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledRowifier,
  StyledCheckbox,
  StyledErrorFor,
  StyledP
} from './styled';
import { Footer } from './Footer';

import { DeleteTokensResult, TestStatus, SaveStatus, Size, MsGraphVersion } from '../common/Enums';
import { IEnv } from './Wizard';
import { SettingsException } from '../common/Exceptions';
import { Settings } from '../common/Settings';

import { Checkbox } from 'office-ui-fabric-react/lib/Checkbox';
import { PrimaryButton, DefaultButton } from 'office-ui-fabric-react/lib/Button';

import { TokenCache, TokenRequest, IToken, TokenRequestV2 } from 'pintra-fx';
import { errorFor } from '../common/Helpers';
import { Strings } from '../strings';
import { CalloutButtonWp } from './CalloutButtonWp';
import { ValidatableTextInput } from './ValidatableTextInput';
import { Dropdown, DropdownMenuItemType, IDropdownOption } from 'office-ui-fabric-react/lib/Dropdown';

export interface IIntegrationTabProps {
  settings: Settings;
  env: IEnv;
}

export interface IIntegrationTabState {
  applicationSecret: string;
  graphVersion: MsGraphVersion;
  enableTokenService: boolean;
  enableNonceCheck: boolean;
  tokensDeleted: DeleteTokensResult;
  errors: SettingsException[];
  testStatus: TestStatus;
  saveStatus: SaveStatus;
}

export class IntegrationTab extends React.Component<IIntegrationTabProps, IIntegrationTabState> {
  public constructor(props: IIntegrationTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      applicationSecret: settings.applicationSecret,
      graphVersion: settings.graphVersion,
      enableNonceCheck: settings.enableNonceCheck,
      enableTokenService: settings.enableTokenService,
      tokensDeleted: DeleteTokensResult.initial,
      testStatus: TestStatus.initial,
      saveStatus: SaveStatus.initial,
      errors: []
    };
  }

  public componentDidUpdate(prevProps: IIntegrationTabProps): void {
    if (this.props.settings === prevProps.settings) return;
    const { settings } = this.props;
    this.setState({
      applicationSecret: settings.applicationSecret,
      graphVersion: settings.graphVersion,
      enableNonceCheck: settings.enableNonceCheck,
      enableTokenService: settings.enableTokenService
    });
  }

  public render(): React.ReactElement<IIntegrationTabProps> {
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>Integration</StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{
              __html: `For the plugin to be able to retrieve information from Microsoft resources e.g. Microsoft
              Graph, SharePoint Online etc. you must perform a number of additional configuration steps:
              <ol><li>You must configure additional permissions for the <strong>registered application in Azure 
              Active Directory</strong> and grant permissions on behalf of all users</li>
              <li>Create an <strong>application secret</strong> and update the settings below accordingly</li></ol>
              A detailed configuration video is available for <a href="https://youtu.be/lzqJSWrybvk?t=250" target="_blank">Azure v2.0</a> 
              (legacy <a href="https://youtu.be/VermwUB5kD0?t=230" target="_blank">Azure v1.0</a>)<br/><br/>
              You must configure this integration if you plan to use on of the following plugin features:
              <ol><li>Client-side solutions can request access tokens e.g. for SharePoint Online and Microsoft Graph 
              <a href="https://www.wpo365.com/pintra-fx/" target="_blank">[details | configuration]</a></li>
              <li>Replace a user's default WordPress avatar with a profile image downloaded from Office 365 
              <a href="https://www.wpo365.com/office-365-profile-picture-as-wp-avatar/" target="_blank">[details | configuration]</a></li>
              <li>Enrich a user's WordPress / BuddyPress profile with information from Azure AD e.g. job title, 
              department and mobile phone <a href="https://www.wpo365.com/extra-buddypress-profile-fields-from-azure-ad/" 
              target="_blank">[details | configuration]</a></li>
              <li>Dynamically assign WordPress user role(s) based on Azure AD group membership(s) 
              <a href="https://www.wpo365.com/role-based-access-using-azure-ad-groups/" target="_blank">[details | configuration]</a></li>
              <li>Deny / allow access based on a user's Azure AD group membership(s) <a href="https://www.wpo365.com/role-based-access-using-azure-ad-groups/" 
              target="_blank">[details | configuration]</a></li>
              <li>User synchronization to enroll new users to WordPress from Azure AD (per user or in batches) 
              <a href="https://www.wpo365.com/synchronize-users-between-office-365-and-wordpress/" target="_blank">[details | configuration]</a></li>
              `
            }}
          />
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Application secret</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => true}
                onValid={(newValue: any) => {
                  this.setState({
                    applicationSecret: newValue as string
                  });
                  return;
                }}
                bound={false}
                defaultValue={this.state.applicationSecret}
                value={this.state.applicationSecret}>
                <CalloutButtonWp
                  wpSlug={'application-secret'}
                  info={{
                    title: 'Application secret',
                    readMore: {
                      title: 'Read more',
                      url: 'https://www.wpo365.com/application-secret/'
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Graph version</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <Dropdown
                  options={[
                    {
                      key: 'select',
                      text: 'Select',
                      itemType: DropdownMenuItemType.Header
                    },
                    { key: 'current', text: 'v1.0' },
                    { key: 'beta', text: 'beta' }
                  ]}
                  selectedKey={this.state.graphVersion}
                  onChange={this.changeGraphVersion}
                />
                <CalloutButtonWp
                  wpSlug={'microsoft-graph-version'}
                  info={{
                    title: 'Authentication scenario',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/microsoft-graph-version/'
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Enable token service</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox onChange={this.toggleEnableTokenService} checked={this.state.enableTokenService} />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={'bearer-token-service'}
                  info={{
                    title: 'Enable token service',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/bearer-token-service/'
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Check nonce</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox onChange={this.toggleEnableCheckNonce} checked={this.state.enableNonceCheck} />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={'check-nonce'}
                  info={{
                    title: 'Check nonce',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/check-nonce/'
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <DefaultButton
                  onClick={() => this.deleteTokens()}
                  iconProps={{
                    iconName: this.state.tokensDeleted == DeleteTokensResult.success ? 'Like' : 'Dislike'
                  }}
                  style={{ minWidth: '400px', minHeight: '30px' }}>
                  Delete tokens
                </DefaultButton>
                <StyledErrorFor>
                  {this.state.tokensDeleted == DeleteTokensResult.error ? 'Could not delete tokens' : null}
                  {this.state.tokensDeleted == DeleteTokensResult.success ? Strings.okTokensDeleted : null}
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <PrimaryButton
                  onClick={() => this.saveConfiguration()}
                  iconProps={{
                    iconName: this.state.testStatus == TestStatus.success ? 'Like' : 'Dislike'
                  }}
                  style={{ minWidth: '400px', minHeight: '30px' }}>
                  Save and test configuration
                </PrimaryButton>
                <StyledErrorFor>
                  {errorFor(this.state.errors, 'saveConfiguration').map((error: SettingsException) => (
                    <StyledP>{error.message}</StyledP>
                  ))}
                  <StyledP>
                    {this.state.saveStatus == SaveStatus.success ? Strings.okSettingsUpdated : null}
                  </StyledP>
                  <StyledP>
                    {this.state.testStatus == TestStatus.success ? Strings.okIntegrationTested : null}
                  </StyledP>
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
      </StyledTable>
    );
  }

  private changeGraphVersion = (event: React.FormEvent<HTMLDivElement>, item: IDropdownOption) => {
    this.setState({ graphVersion: item.key as MsGraphVersion });
  };

  private toggleEnableTokenService = (): void => {
    this.setState((prevState: IIntegrationTabState, props: IIntegrationTabProps) => {
      return { enableTokenService: !prevState.enableTokenService };
    });
  };

  private toggleEnableCheckNonce = (): void => {
    this.setState((prevState: IIntegrationTabState, props: IIntegrationTabProps) => {
      return { enableNonceCheck: !prevState.enableNonceCheck };
    });
  };

  private async deleteTokens(): Promise<null> {
    if (
      !confirm(
        [
          'You are about to delete the server-side cached O365 access and refresh tokens for all users.',
          'After deleting all tokens, users should log in interactively for the plugin to be able to retrieve',
          '(a) new access token(s) once their current tokens expire.'
        ].join(' ')
      )
    )
      return;
    TokenCache.clearTokenCache();
    const data = new FormData();
    data.append('action', 'delete_tokens');
    data.append('nonce', this.props.env.nonce);
    let response: any;
    try {
      response = await Axios.post(this.props.env.wpAjaxAdminUrl, data);
    } catch (error) {
      this.setState({ tokensDeleted: DeleteTokensResult.error });
      return null;
    }
    if (response.status == 200) {
      if (response.data && response.data.status && response.data.status == 'OK') {
        this.setState({ tokensDeleted: DeleteTokensResult.success });
        return null;
      }
    }
    this.setState({ tokensDeleted: DeleteTokensResult.error });
    return null;
  }

  private testIntegration = (): void => {
    this.setState({
      errors: [],
      testStatus: TestStatus.testing
    });
    const { useV2 } = this.props.settings;
    if (useV2) this.getTokenV2().then(this.evaluateTokenResponse);
    else this.getToken().then(this.evaluateTokenResponse);
  };

  private evaluateTokenResponse = (token: IToken | Error) => {
    if (token instanceof Error) {
      this.setState((prevState: IIntegrationTabState, props: IIntegrationTabProps) => {
        return {
          errors: [
            ...prevState.errors,
            new SettingsException('saveConfiguration', `Please check log. Innerexception: ${token.message}`, 5000)
          ],
          testStatus: TestStatus.error
        };
      });
    } else {
      this.setState({
        errors: [],
        testStatus: TestStatus.success
      });
    }
  };

  private async getTokenV2(): Promise<IToken | Error> {
    TokenCache.clearTokenCache();
    const tokenResponse = await TokenCache.getTokenV2(
      new TokenRequestV2('graphUserRead', 'https://graph.microsoft.com/User.Read'),
      {
        nonce: this.props.env.nonce /* a random value generated by WordPress and injected by Pintra Framework */,
        wpAjaxAdminUrl: this.props.env.wpAjaxAdminUrl /* e.g. http://localhost/website/wp-admin/admin-ajax.php */
      }
    );

    if (tokenResponse instanceof Error) return new Error(tokenResponse.message);
    return tokenResponse as IToken;
  }

  private async getToken(): Promise<IToken | Error> {
    // First empty the local storage
    localStorage.removeItem('pintraTokenCache');
    const tokenResponse = await TokenCache.getToken(new TokenRequest('graph', 'https://graph.microsoft.com'), {
      nonce: this.props.env.nonce /* a random value generated by WordPress and injected by Pintra Framework */,
      wpAjaxAdminUrl: this.props.env.wpAjaxAdminUrl /* e.g. http://localhost/website/wp-admin/admin-ajax.php */
    });

    if (tokenResponse instanceof Error) return new Error(tokenResponse.message);
    return tokenResponse as IToken;
  }

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.applicationSecret = this.state.applicationSecret;
    settings.graphVersion = this.state.graphVersion;
    settings.enableTokenService = this.state.enableTokenService;
    settings.enableNonceCheck = this.state.enableNonceCheck;

    const updateResult = await Settings.updateSettings(this.props.env, settings);

    if (updateResult.success) {
      this.setState({
        errors: [],
        testStatus: TestStatus.initial,
        saveStatus: SaveStatus.success
      });
      if (this.state.enableTokenService) this.testIntegration();
      else
        this.setState({
          errors: [new SettingsException('saveConfiguration', Strings.infoTokenServiceDisabled, 2000)]
        });
    } else {
      this.setState((prevState: IIntegrationTabState, props: IIntegrationTabProps) => {
        return {
          errors: [...prevState.errors, new SettingsException('saveConfiguration', updateResult.message, 2000)],
          testStatus: TestStatus.initial,
          saveStatus: SaveStatus.error
        };
      });
    }
  }
}
