import * as React from 'react';

import {
  StyledList,
  StyledListRow,
  StyledIconButton,
  StyledRowifier,
  StyledErrorFor,
  StyledP
} from './styled';

import { Icon } from 'office-ui-fabric-react/lib/Icon';
import { IconNames } from '@uifabric/icons';
import { ValidatableTextInput } from './ValidatableTextInput';
import { SettingsException } from '../common/Exceptions';
import { Strings } from '../strings';
import { Size } from '../common/Enums';

export interface IListEditorProps {
  list: string[];
  onListChanged: (list: string[]) => void;
  placeholder?: string;
  isValid?: RegExp;
  errorMessage?: string;
}

export interface IListEditorState {
  list: string[];
  errors: SettingsException[];
  isValid: boolean;
  value: string;
}

export class ListEditor extends React.Component<
  IListEditorProps,
  IListEditorState
> {
  public constructor(props: IListEditorProps) {
    super(props);
    this.state = {
      list: this.props.list,
      errors: [],
      isValid: true,
      value: ''
    };
  }

  public componentDidUpdate(prevProps: IListEditorProps): void {
    if (this.props.list === prevProps.list) return;
    this.setState({ list: this.props.list });
  }

  public render(): React.ReactElement<IListEditorProps> {
    const { isValid, errorMessage, placeholder } = this.props;
    return (
      <StyledRowifier>
        <StyledList>
          {this.state.list.map((item: string, idx: number) => {
            return (
              <StyledListRow key={idx}>
                <ValidatableTextInput
                  size={Size.large}
                  defaultValue={item}
                  value={item}
                  bound={true}
                  disabled={true}
                />
                <StyledIconButton onClick={() => this.deleteItem(idx)}>
                  <Icon
                    iconName={IconNames.Delete}
                    title={'Delete'}
                    ariaLabel={'Click to delete element'}
                    style={{
                      fontSize: '12px',
                      lineHeight: '30px',
                      height: '30px',
                      width: '30px',
                      margin: '0px 0px 0px -30px',
                      padding: 0,
                      cursor: 'pointer'
                    }}
                  />
                </StyledIconButton>
              </StyledListRow>
            );
          })}
          <StyledListRow>
            <ValidatableTextInput
              size={Size.large}
              defaultValue={Strings.empty}
              value={this.state.value}
              bound={true}
              isValid={(oldValue: any, newValue: any) => {
                if (
                  isValid &&
                  newValue &&
                  (newValue as string) != Strings.empty
                )
                  return isValid.test(newValue as string);
                return true;
              }}
              onValid={this.onValid}
              onError={this.onError}
              errorMessage={errorMessage}
              placeholder={placeholder ? placeholder : null}
            />
            <StyledIconButton onClick={this.addItem}>
              <Icon
                iconName={IconNames.Add}
                title={'Add'}
                ariaLabel={'Click to add this item'}
                style={{
                  fontSize: '12px',
                  lineHeight: '30px',
                  height: '30px',
                  width: '30px',
                  margin: '0px 0px 0px -30px',
                  padding: 0,
                  cursor: 'pointer'
                }}
              />
            </StyledIconButton>
          </StyledListRow>
        </StyledList>
        {this.props.children}
        <StyledErrorFor>
          {this.state.errors.map((error: SettingsException) => (
            <StyledP>{error.message}</StyledP>
          ))}
        </StyledErrorFor>
      </StyledRowifier>
    );
  }

  private onError = (errorMessage: string) => {
    this.setState({
      errors: [new SettingsException(null, errorMessage)],
      isValid: false
    });
  };

  private onValid = (newValue: any) => {
    this.setState({ isValid: true, value: newValue as string, errors: [] });
  };

  private addItem = () => {
    const { list } = this.state;
    if (this.state.isValid && this.state.value.length > 0) {
      const newList = [...list, this.state.value];
      this.setState({ list: newList, value: '' });
      this.props.onListChanged(newList);
    }
  };

  private deleteItem = (idx: number) => {
    const newList = [...this.state.list];
    newList.splice(idx, 1);
    this.setState({ list: newList });
    this.props.onListChanged(newList);
  };
}
