import * as React from 'react';

import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledRowifier,
  StyledCheckbox,
  StyledErrorFor,
  StyledP
} from './styled';

import { ValidatableTextInput } from './ValidatableTextInput';
import { Strings } from '../strings';
import { CalloutButtonWp } from './CalloutButtonWp';
import { IEnv } from './Wizard';
import { Settings } from '../common/Settings';
import { SettingsException } from '../common/Exceptions';
import { SaveStatus, Size } from '../common/Enums';
import { Footer } from './Footer';
import { KeyValuePair } from '../common/KeyValuePair';
import { Checkbox } from 'office-ui-fabric-react/lib/Checkbox';
import { KeyValueEditor } from './KeyValueEditor';
import { PrimaryButton } from 'office-ui-fabric-react/lib/Button';
import { errorFor } from '../common/Helpers';
import { IAdditionalInfoDialogProps } from '../common/IAdditionalInfoDialogProps';
import { AdditionalInfoDialog } from './AdditionalInfoDialog';

export interface IUserSyncTabProps {
  env: IEnv;
  settings: Settings;
}

export interface IUserSyncTabState {
  graphUserDetails: boolean;
  extraUserFields: KeyValuePair[];
  avatarUpdated: number;
  allGroupMemberships: boolean;
  useAvatar: boolean;
  useBpAvatar: boolean;
  userSyncQuery: string;
  enableUserSync: boolean;
  errors: SettingsException[];
  saveStatus: SaveStatus;
  additionalInfo: IAdditionalInfoDialogProps | null;
}

export class UserSyncTab extends React.Component<IUserSyncTabProps, IUserSyncTabState> {
  public constructor(props: IUserSyncTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      graphUserDetails: settings.graphUserDetails,
      extraUserFields: settings.extraUserFields,
      avatarUpdated: settings.avatarUpdated,
      allGroupMemberships: settings.allGroupMemberships,
      useAvatar: settings.useAvatar,
      useBpAvatar: settings.useBpAvatar,
      userSyncQuery: settings.userSyncQuery,
      enableUserSync: settings.enableUserSync,
      errors: [],
      saveStatus: SaveStatus.initial,
      additionalInfo: null
    };
  }

  public componentDidUpdate(prevProps: IUserSyncTabProps): void {
    if (this.props.settings === prevProps.settings) return;
    const { settings } = this.props;
    this.setState({
      graphUserDetails: settings.graphUserDetails,
      extraUserFields: settings.extraUserFields,
      avatarUpdated: settings.avatarUpdated,
      allGroupMemberships: settings.allGroupMemberships,
      enableUserSync: settings.enableUserSync,
      useAvatar: settings.useAvatar,
      useBpAvatar: settings.useBpAvatar,
      userSyncQuery: settings.userSyncQuery
    });
  }

  public render(): React.ReactElement<IUserSyncTabProps> {
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>User Synchronization</StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{
              __html: `Use this tab to configure user synchronization and related capabilities such as enriching a user's
            profile with information retrieved from Azure AD and replace a user's default WordPress avatar with a profile 
            image downloaded from Office 365.`
            }}
          />
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Enable user sync</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox onChange={this.toggleEnableUserSync} checked={this.state.enableUserSync} />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={'user-synchronization'}
                  info={{
                    title: 'User Synchronization',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/user-synchronization/'
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Retrieve all group memberships</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox onChange={this.toggleAllGroupMemberships} checked={this.state.allGroupMemberships} />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={'all-group-memberships'}
                  info={{
                    title: 'Retrieve all group memberships',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/all-group-memberships/'
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>User sync query</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => true}
                onValid={(newValue: any) => {
                  this.setState({
                    userSyncQuery: newValue as string
                  });
                  return;
                }}
                bound={false}
                defaultValue={this.state.userSyncQuery}
                value={this.state.userSyncQuery}>
                <CalloutButtonWp
                  wpSlug={'user-sync-query'}
                  info={{
                    title: 'User sync query',
                    readMore: {
                      title: 'Read more',
                      url: 'https://www.wpo365.com/user-sync-query/'
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Show O365 user fields</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox onChange={this.toggleGraphUserDetails} checked={this.state.graphUserDetails} />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={'show-o365-user-fields'}
                  info={{
                    title: 'Show O365 user fields',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/show-o365-user-fields/'
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>O365 user fields</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <KeyValueEditor
                  list={this.state.extraUserFields}
                  onListChanged={(list: KeyValuePair[]) => this.setState({ extraUserFields: list })}
                  keyPlaceholder={Strings.infoExtraUserFieldsKey}
                  valuePlaceholder={Strings.infoExtraUserFieldsValue}>
                  <CalloutButtonWp
                    wpSlug={'extra-user-fields'}
                    info={{
                      title: 'Extra user fields',
                      readMore: {
                        title: 'Read online',
                        url: 'https://www.wpo365.com/extra-user-fields/'
                      }
                    }}
                  />
                </KeyValueEditor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Use O365 Avatar</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox onChange={this.toggleUseAvatar} checked={this.state.useAvatar} />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={'use-o365-avatar'}
                  info={{
                    title: 'Use O365 Avatar',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/use-o365-avatar/'
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Use O365 Avatar (Buddy Press)</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledRowifier>
                <StyledCheckbox>
                  <Checkbox onChange={this.toggleUseBpAvatar} checked={this.state.useBpAvatar} />
                </StyledCheckbox>
                <CalloutButtonWp
                  wpSlug={'use-bp-avatar'}
                  info={{
                    title: 'Use O365 Avatar (Buddy Press)',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/use-bp-avatar/'
                    }
                  }}
                />
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Avatar refresh</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                isValid={(oldValue: any, newValue: any) => {
                  return /^\d+$/.test(newValue as string); // validate numeric input
                }}
                onValid={(newValue: any) => {
                  this.setState({
                    avatarUpdated: parseInt(newValue)
                  });
                  return;
                }}
                bound={false}
                errorMessage={Strings.errAvatarUpdatedMustBeNumeric}
                defaultValue={this.state.avatarUpdated.toString()}
                value={this.state.avatarUpdated.toString()}
                placeholder={Strings.infoAvatarUpdated}>
                <CalloutButtonWp
                  wpSlug={'avatar-refresh'}
                  info={{
                    title: 'Avatar refresh',
                    readMore: {
                      title: 'Read online',
                      url: 'https://www.wpo365.com/avatar-refresh/'
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <PrimaryButton
                  onClick={() => this.saveConfiguration()}
                  iconProps={{
                    iconName: this.state.saveStatus == SaveStatus.success ? 'Like' : 'Dislike'
                  }}
                  style={{ minWidth: '400px', minHeight: '30px' }}
                  disabled={this.state.saveStatus == SaveStatus.saving}>
                  Save configuration
                </PrimaryButton>
                <StyledErrorFor>
                  {errorFor(this.state.errors, 'saveConfiguration').map((error: SettingsException) => (
                    <StyledP>{error.message}</StyledP>
                  ))}
                  {this.state.saveStatus == SaveStatus.success ? Strings.okSettingsUpdated : null}
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        )}
      </StyledTable>
    );
  }

  private toggleAllGroupMemberships = (): void => {
    this.setState((prevState: IUserSyncTabState, props: IUserSyncTabProps) => {
      return { allGroupMemberships: !prevState.allGroupMemberships };
    });
  };

  private toggleGraphUserDetails = (): void => {
    this.setState((prevState: IUserSyncTabState, props: IUserSyncTabProps) => {
      return { graphUserDetails: !prevState.graphUserDetails };
    });
  };

  private toggleUseAvatar = (): void => {
    const { useAvatar } = this.state;
    if (useAvatar) {
      this.setState({
        useAvatar: false
      });
      return;
    }
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleUseAvatar,
      subText: Strings.subTextUseAvatar,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () =>
        this.setState({
          additionalInfo: null,
          useAvatar: true
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  };

  private toggleUseBpAvatar = (): void => {
    const { useBpAvatar } = this.state;
    if (useBpAvatar) {
      this.setState({
        useBpAvatar: false
      });
      return;
    }
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleUseBpAvatar,
      subText: Strings.subTextUseBpAvatar,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () =>
        this.setState({
          additionalInfo: null,
          useAvatar: true,
          useBpAvatar: true
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  };

  private toggleEnableUserSync = (): void => {
    const { enableUserSync } = this.state;
    if (enableUserSync) {
      this.setState({
        enableUserSync: false
      });
      return;
    }
    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleEnableUserSync,
      subText: Strings.subTextEnableUserSync,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: () =>
        this.setState({
          additionalInfo: null
        }),
      onConfirm: () =>
        this.setState({
          additionalInfo: null,
          enableUserSync: true
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  };

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.graphUserDetails = this.state.graphUserDetails;
    settings.extraUserFields = this.state.extraUserFields;
    settings.allGroupMemberships = this.state.allGroupMemberships;
    settings.useAvatar = this.state.useAvatar;
    settings.useBpAvatar = this.state.useBpAvatar;
    settings.userSyncQuery = this.state.userSyncQuery;
    settings.avatarUpdated = this.state.avatarUpdated;
    settings.enableUserSync = this.state.enableUserSync;

    const updateResult = await Settings.updateSettings(this.props.env, settings);

    if (updateResult.success) {
      this.setState({
        errors: [],
        saveStatus: SaveStatus.success
      });
    } else {
      this.setState((prevState: IUserSyncTabState, props: IUserSyncTabProps) => {
        return {
          errors: [...prevState.errors, new SettingsException('saveConfiguration', updateResult.message, 2000)],
          saveStatus: SaveStatus.error
        };
      });
    }
  }
}
