import * as React from 'react';

import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledRowifier,
  StyledErrorFor,
  StyledP
} from './styled';

import { ValidatableTextInput } from './ValidatableTextInput';
import { Strings } from '../strings';
import { CalloutButtonWp } from './CalloutButtonWp';
import { IEnv } from './Wizard';
import { Settings } from '../common/Settings';
import { SettingsException } from '../common/Exceptions';
import { SaveStatus, Size } from '../common/Enums';
import { Footer } from './Footer';
import { PrimaryButton } from 'office-ui-fabric-react/lib/Button';
import { errorFor } from '../common/Helpers';
import Axios from 'axios';
import { AdditionalInfoDialog } from './AdditionalInfoDialog';
import { IAdditionalInfoDialogProps } from '../common/IAdditionalInfoDialogProps';

export interface IDownloadsTabProps {
  env: IEnv;
  settings: Settings;
}

export interface IDownloadsTabState {
  licenseKey: string;
  errors: SettingsException[];
  saveStatus: SaveStatus;
  //-----
  additionalInfo: IAdditionalInfoDialogProps | null;
}

export class DownloadsTab extends React.Component<IDownloadsTabProps, IDownloadsTabState> {
  public constructor(props: IDownloadsTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      licenseKey: settings.licenseKey,
      errors: [],
      saveStatus: SaveStatus.initial,
      additionalInfo: null
    };
  }

  public componentDidUpdate(prevProps: IDownloadsTabProps): void {
    if (this.props.settings === prevProps.settings) return;
    const { settings } = this.props;
    this.setState({
      licenseKey: settings.licenseKey
    });
  }

  public render(): React.ReactElement<IDownloadsTabProps> {
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>Downloads</StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{
              __html: `Use this tab to enter the license key that you have received in the
              initial <strong>WordPress + Office 365 purchase receipt</strong> email. 
              When you enter your license key, the plugin will try and activate it immediately for you. When
              activated, the license will remain valid for at least one year (or for as long as you
              (automatically) renew the software subscription). Only when you have entered a valid
              license and activated it, will you be able to download, install and activate future updates
              of this plugin.<br/><br/>If you don't know your license (anymore) then you can always
              log back into your acocunt on the <a target"_blank" href="https://www.wpo365.com/your-account/">
              WordPress + Office 365 website</a>.`
            }}
          />
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>License key</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                bound={false}
                defaultValue={this.state.licenseKey}
                value={this.state.licenseKey}
                placeholder={Strings.infoLicenseKey}
                isValid={(oldValue: any, newValue: any) => true}
                onValid={(newValue: any) => {
                  this.setState({
                    licenseKey: newValue as string
                  });
                  return;
                }}>
                <CalloutButtonWp
                  wpSlug={'license-key'}
                  info={{
                    title: 'License key',
                    readMore: {
                      title: 'Read more',
                      url: 'https://www.wpo365.com/license-key/'
                    }
                  }}
                />
              </ValidatableTextInput>
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <PrimaryButton
                  onClick={() => this.saveConfiguration()}
                  iconProps={{
                    iconName: this.state.saveStatus == SaveStatus.success ? 'Like' : 'Dislike'
                  }}
                  style={{ minWidth: '400px', minHeight: '30px' }}
                  disabled={this.state.saveStatus == SaveStatus.saving}>
                  Try activate license
                </PrimaryButton>
                <StyledErrorFor>
                  {errorFor(this.state.errors, 'saveConfiguration').map((error: SettingsException) => (
                    <StyledP>{error.message}</StyledP>
                  ))}
                  {this.state.saveStatus == SaveStatus.success ? Strings.okSettingsUpdated : null}
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        )}
      </StyledTable>
    );
  }

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.licenseKey = this.state.licenseKey;
    const updateResult = await Settings.updateSettings(this.props.env, settings);

    if (updateResult.success) {
      this.setState({
        errors: [],
        saveStatus: SaveStatus.success
      });
      // After saving try to activate the license
      this.activateLicense();
    } else {
      this.setState((prevState: IDownloadsTabState, props: IDownloadsTabProps) => {
        return {
          errors: [...prevState.errors, new SettingsException('saveConfiguration', updateResult.message, 2000)],
          saveStatus: SaveStatus.error
        };
      });
    }
  }

  public async activateLicense(): Promise<void> {
    const { env } = this.props;
    const data = new FormData();
    data.append('action', 'activate_license');
    data.append('nonce', env.nonce);

    let response: any;

    try {
      response = await Axios.post(env.wpAjaxAdminUrl, data);
    } catch (error) {
      return;
    }

    const licenseActivated =
      response.status == 200 && response.data && response.data.status && response.data.status == 'OK';

    const subText = licenseActivated
      ? Strings.subTextLicenseActivated
      : response.status == 200 && response.data && response.data.status && response.data.status == 'NOK'
      ? response.data.message
      : Strings.subTextLicenseActivatedUnknowError;

    const confirm: IAdditionalInfoDialogProps = {
      title: Strings.titleLicenseActivation,
      subText,
      onDismiss: () =>
        this.setState({
          additionalInfo: null
        }),
      onCancel: null,
      onConfirm: () =>
        this.setState({
          additionalInfo: null
        })
    };
    this.setState({
      additionalInfo: confirm
    });
  }
}
