import * as React from 'react';

import {
  StyledTableCaption,
  StyledTable,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledRowifier,
  StyledCheckbox,
  StyledTextArea
} from './styled';

import { IEnv } from './Wizard';
import { ValidatableTextInput } from './ValidatableTextInput';
import { Strings } from '../strings';
import { Footer } from './Footer';

import { Checkbox } from 'office-ui-fabric-react/lib/Checkbox';
import { DefaultButton } from 'office-ui-fabric-react/lib/Button';
import { IAdditionalInfoDialogProps } from '../common/IAdditionalInfoDialogProps';
import { AdditionalInfoDialog } from './AdditionalInfoDialog';
import { DropdownMenuItemType, Dropdown, IDropdownOption } from 'office-ui-fabric-react/lib/Dropdown';
import { SharePointHome } from '../common/SharePointHome';
import { Size, PluginVersion } from '../common/Enums';
import { Equal, NotEqual } from './Equal';

export interface ICbsConfiguratorProps {
  env: IEnv;
}

export interface ICbsConfiguratorState {
  sharePointHome: string;
  sharePointHomeError: boolean;
  scriptSrc: string;
  pageSize: number;
  minQueryLength: number;
  addWildcard: boolean;
  autoSearch: boolean;
  hideSearchBox: boolean;
  hideMessageBar: boolean;
  hidePaging: boolean;
  resultSourceId: string;
  resultSourceIdReadOnly: boolean;
  primaryHtml: string;
  secondaryHtml: string;
  tertiaryHtml: string;
  metaHtml: string;
  hideContactActionButtons: boolean;
  showContactActionButtons: boolean;
  emailProperty?: string;
  workPhoneProperty?: string;
  imProperty?: string;
  mobilePhoneProperty?: string;
  getUserPhoto: boolean;
  selectProperties: string;
  queryTemplate: string;
  sortList: string;
  copyButtonCaption: string;
}

export class CbsConfigurator extends React.Component<ICbsConfiguratorProps, ICbsConfiguratorState> {
  public constructor(props: ICbsConfiguratorProps) {
    super(props);
    const { env } = this.props;
    this.state = {
      sharePointHome: '',
      sharePointHomeError: false,
      scriptSrc: this.currentScriptSrc('wizard.js').replace(
        'wizard.js',
        env.props.pluginVersion === PluginVersion.wpo365LoginIntranet ? 'cbs.js' : 'cbsBasic.js'
      ),
      pageSize: 10,
      minQueryLength: 3,
      addWildcard: false,
      autoSearch: false,
      hideSearchBox: false,
      hideMessageBar: false,
      hidePaging: false,
      resultSourceId: '8413cd39-2156-4e00-b54d-11efd9abdb89',
      resultSourceIdReadOnly: true,
      primaryHtml: '__##Title##__',
      secondaryHtml: '__##HitHighlightedSummary##__',
      tertiaryHtml: '',
      metaHtml: 'Modified: __##lastModified##__',
      getUserPhoto: false,
      selectProperties: '',
      hideContactActionButtons: true,
      showContactActionButtons: true,
      emailProperty: 'WorkEmail',
      workPhoneProperty: 'WorkPhone',
      imProperty: 'UserName',
      mobilePhoneProperty: 'MobilePhone',
      queryTemplate: '{searchterms}',
      sortList: 'Rank:descending',
      // ----- //
      copyButtonCaption: Strings.capCopyShortcodeButton
    };
    this.getSharePointHome();
  }

  public render(): React.ReactElement<ICbsConfiguratorProps> {
    const { copyButtonCaption } = this.state;
    const { env } = this.props;
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>Content by Search (SharePoint Online) shortcode generator</StyledTableHeader>
          <StyledTableIntro>
            To create the shortcode you can use this generator. Simply configure the options and copy the shortcode
            to the clipboard. Then all you need to do is to add the shortcode to a page and you're ready!
            <br />
            <br />
            Please consult the online documentation{' '}
            <a href={'https://www.wpo365.com/content-by-search/'}>https://www.wpo365.com/content-by-search/</a> for
            instructions on how to generate a shortcode and place it on any WordPress post or page.
          </StyledTableIntro>
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>Your SharePoint Home URL</StyledTableCell>
            <StyledTableCell>
              <ValidatableTextInput
                size={Size.large}
                bound={false}
                isValid={(oldValue: any, newValue: any) => {
                  return (newValue as string).indexOf('https://') === 0;
                }}
                onValid={(newValue: any) => {
                  this.setState({ sharePointHome: newValue as string });
                }}
                errorMessage={'SharePoint Home must start with https e.g. https://your-tenant.sharepoint.com'}
                defaultValue={'https://'}
                value={this.state.sharePointHome}
                disabled={!this.state.sharePointHomeError}
              />
            </StyledTableCell>
          </StyledTableRow>
          <Equal a={env.props.pluginVersion} b={PluginVersion.wpo365LoginIntranet}>
            <StyledTableRow>
              <StyledTableCell>Select result source</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Dropdown
                    style={{ width: '400px' }}
                    placeHolder={'Please select a result source from the list'}
                    options={[
                      { key: '8413cd39-2156-4e00-b54d-11efd9abdb89', text: 'Local SharePoint Results' },
                      { key: 'b09a7990-05ea-4af9-81ef-edfab16c4e31', text: 'Local People Results' },
                      { key: 'other', text: 'Enter manually' }
                    ]}
                    onChange={(event: React.FormEvent<HTMLDivElement>, item: IDropdownOption) => {
                      const selectedKey = item.key.toString();
                      const isSpResults = selectedKey == '8413cd39-2156-4e00-b54d-11efd9abdb89';
                      const isPplResults = selectedKey == 'b09a7990-05ea-4af9-81ef-edfab16c4e31';
                      this.setState({
                        resultSourceIdReadOnly: selectedKey != 'other',
                        primaryHtml: isSpResults ? '__##Title##__' : isPplResults ? '__##PreferredName##__' : '',
                        secondaryHtml: isSpResults
                          ? '__##HitHighlightedSummary##__'
                          : isPplResults
                          ? '__##JobTitle##__'
                          : '',
                        tertiaryHtml: isSpResults ? '' : isPplResults ? '__##Department##__' : '',
                        metaHtml: isSpResults ? 'Modified: __##lastModified##__' : isPplResults ? '' : '',
                        resultSourceId: item.key == 'other' ? '' : item.key.toString(),
                        selectProperties: isPplResults
                          ? 'UserName|JobTitle|Department|WorkPhone|WorkEmail|MobilePhone'
                          : '',
                        hideContactActionButtons: isPplResults ? false : true
                      });
                    }}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Result source ID</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => this.setState({ resultSourceId: newValue })}
                  value={this.state.resultSourceId}
                  defaultValue={this.state.resultSourceId}
                  disabled={this.state.resultSourceIdReadOnly}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Primary text</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => this.setState({ primaryHtml: newValue })}
                  defaultValue={this.state.primaryHtml}
                  value={this.state.primaryHtml}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Secondary text</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => this.setState({ secondaryHtml: newValue })}
                  defaultValue={this.state.secondaryHtml}
                  value={this.state.secondaryHtml}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Tertiary text</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => this.setState({ tertiaryHtml: newValue })}
                  defaultValue={this.state.tertiaryHtml}
                  value={this.state.tertiaryHtml}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Meta text</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => this.setState({ metaHtml: newValue })}
                  defaultValue={this.state.metaHtml}
                  value={this.state.metaHtml}
                />
              </StyledTableCell>
            </StyledTableRow>
            {!this.state.hideContactActionButtons && (
              <React.Fragment>
                <StyledTableRow>
                  <StyledTableCell>Show contact action buttons</StyledTableCell>
                  <StyledTableCell>
                    <StyledRowifier>
                      <StyledCheckbox>
                        <Checkbox
                          onChange={(event: React.MouseEvent<HTMLElement>) =>
                            this.setState((prevState: ICbsConfiguratorState) => {
                              return { showContactActionButtons: !prevState.showContactActionButtons };
                            })
                          }
                          checked={this.state.showContactActionButtons}
                        />
                      </StyledCheckbox>
                    </StyledRowifier>
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Mobile phone property</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) => this.setState({ mobilePhoneProperty: newValue })}
                      defaultValue={this.state.mobilePhoneProperty}
                      value={this.state.mobilePhoneProperty}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Work phone property</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) => this.setState({ workPhoneProperty: newValue })}
                      defaultValue={this.state.workPhoneProperty}
                      value={this.state.workPhoneProperty}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>IM property</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) => this.setState({ imProperty: newValue })}
                      defaultValue={this.state.imProperty}
                      value={this.state.imProperty}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Email property</StyledTableCell>
                  <StyledTableCell>
                    <ValidatableTextInput
                      size={Size.large}
                      bound={false}
                      isValid={(oldValue: any, newValue: any) => {
                        return true;
                      }}
                      onValid={(newValue: any) => this.setState({ emailProperty: newValue })}
                      defaultValue={this.state.emailProperty}
                      value={this.state.emailProperty}
                    />
                  </StyledTableCell>
                </StyledTableRow>
                <StyledTableRow>
                  <StyledTableCell>Try retrieve user photo</StyledTableCell>
                  <StyledTableCell>
                    <StyledRowifier>
                      <StyledCheckbox>
                        <Checkbox
                          onChange={(event: React.MouseEvent<HTMLElement>) =>
                            this.setState((prevState: ICbsConfiguratorState) => {
                              return { getUserPhoto: !prevState.getUserPhoto };
                            })
                          }
                          checked={this.state.getUserPhoto}
                        />
                      </StyledCheckbox>
                    </StyledRowifier>
                  </StyledTableCell>
                </StyledTableRow>
              </React.Fragment>
            )}
            <StyledTableRow>
              <StyledTableCell>Query template</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => this.setState({ queryTemplate: newValue })}
                  defaultValue={'{searchterms}'}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Auto search</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={(event: React.MouseEvent<HTMLElement>) =>
                        this.setState((prevState: ICbsConfiguratorState) => {
                          return { autoSearch: !prevState.autoSearch };
                        })
                      }
                      checked={this.state.autoSearch}
                    />
                  </StyledCheckbox>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Select properties</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => this.setState({ selectProperties: newValue })}
                  defaultValue={''}
                  value={this.state.selectProperties}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Results per page</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    const newValueAsInt = parseInt(newValue);
                    return (
                      (newValue as string) === newValueAsInt.toString() && newValueAsInt > 0 && newValueAsInt < 50
                    );
                  }}
                  onValid={(newValue: any) => this.setState({ pageSize: parseInt(newValue) })}
                  errorMessage={'pageSize must be number greater than 0 and smaller than 50'}
                  defaultValue={'10'}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Min. input length</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    const newValueAsInt = parseInt(newValue);
                    return (
                      (newValue as string) === newValueAsInt.toString() && newValueAsInt > 0 && newValueAsInt < 5
                    );
                  }}
                  onValid={(newValue: any) => this.setState({ minQueryLength: parseInt(newValue) })}
                  errorMessage={'minQueryLength must be number greater than 0 and smaller than 5'}
                  defaultValue={'3'}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Add wildcard to each search term</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <StyledCheckbox>
                    <Checkbox
                      onChange={(event: React.MouseEvent<HTMLElement>) =>
                        this.setState((prevState: ICbsConfiguratorState) => {
                          return { addWildcard: !prevState.addWildcard };
                        })
                      }
                      checked={this.state.addWildcard}
                    />
                  </StyledCheckbox>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Hide search box</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Checkbox
                    onChange={(event: React.MouseEvent<HTMLElement>) =>
                      this.setState((prevState: ICbsConfiguratorState) => {
                        return { hideSearchBox: !prevState.hideSearchBox };
                      })
                    }
                    checked={this.state.hideSearchBox}
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Hide message bar</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Checkbox
                    checked={this.state.hideMessageBar}
                    onChange={(event: React.MouseEvent<HTMLElement>) =>
                      this.setState((prevState: ICbsConfiguratorState) => {
                        return { hideMessageBar: !prevState.hideMessageBar };
                      })
                    }
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Hide paging</StyledTableCell>
              <StyledTableCell>
                <StyledRowifier>
                  <Checkbox
                    checked={this.state.hidePaging}
                    onChange={(event: React.MouseEvent<HTMLElement>) =>
                      this.setState((prevState: ICbsConfiguratorState) => {
                        return { hidePaging: !prevState.hidePaging };
                      })
                    }
                  />
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Sort list</StyledTableCell>
              <StyledTableCell>
                <ValidatableTextInput
                  size={Size.large}
                  bound={false}
                  isValid={(oldValue: any, newValue: any) => {
                    return true;
                  }}
                  onValid={(newValue: any) => this.setState({ sortList: newValue })}
                  defaultValue={'Rank:descending'}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell>Shortcode</StyledTableCell>
              <StyledTableCell>
                <StyledTextArea
                  readOnly={true}
                  value={`[pintra props="resource,${this.state.sharePointHome};pageSize,${
                    this.state.pageSize
                  };minQueryLength,${this.state.minQueryLength};addWildcard,${
                    this.state.addWildcard ? 'true' : 'false'
                  };autoSearch,${this.state.autoSearch ? 'true' : 'false'};hideMessageBar,${
                    this.state.hideMessageBar ? 'true' : 'false'
                  };hideSearchBox,${this.state.hideSearchBox ? 'true' : 'false'};hidePaging,${
                    this.state.hidePaging ? 'true' : 'false'
                  };resultSourceId,${this.state.resultSourceId};queryTemplate,${
                    this.state.queryTemplate
                  };primaryHtml,${this.state.primaryHtml};secondaryHtml,${this.state.secondaryHtml};tertiaryHtml,${
                    this.state.tertiaryHtml
                  };metaHtml,${this.state.metaHtml};sortList,${this.state.sortList};selectProperties,${
                    this.state.selectProperties
                  };showContactActionButtons,${
                    this.state.showContactActionButtons ? 'true' : 'false'
                  };workPhoneProperty,${this.state.workPhoneProperty};mobilePhoneProperty,${
                    this.state.mobilePhoneProperty
                  };emailProperty,${this.state.emailProperty};imProperty,${this.state.imProperty};getUserPhoto,${
                    this.state.getUserPhoto ? 'true' : 'false'
                  };home,${
                    this.state.sharePointHome
                  };pluginServerRelativeUrl,${this.getPluginServerRelativeUrl()}" script_url="${
                    this.state.scriptSrc
                  }"]`}
                  className={'pintraSpoShortCode'}
                />
              </StyledTableCell>
            </StyledTableRow>
          </Equal>
          <NotEqual a={env.props.pluginVersion} b={PluginVersion.wpo365LoginIntranet}>
            <StyledTableRow>
              <StyledTableCell>Shortcode</StyledTableCell>
              <StyledTableCell>
                <StyledTextArea
                  readOnly={true}
                  value={`[pintra props="resource,${this.state.sharePointHome};home,${
                    this.state.sharePointHome
                  };pluginServerRelativeUrl,${this.getPluginServerRelativeUrl()}" script_url="${
                    this.state.scriptSrc
                  }"]`}
                  className={'pintraSpoShortCode'}
                />
              </StyledTableCell>
            </StyledTableRow>
          </NotEqual>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <DefaultButton
                  onClick={this.copyShortcode}
                  iconProps={{ iconName: 'Copy' }}
                  style={{ minWidth: '400px', minHeight: '30px' }}>
                  {copyButtonCaption}
                </DefaultButton>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
        {/* this.state.additionalInfo && (
          <AdditionalInfoDialog
            onCancel={this.state.additionalInfo.onCancel}
            onConfirm={this.state.additionalInfo.onConfirm}
            onDismiss={this.state.additionalInfo.onDismiss}
            title={this.state.additionalInfo.title}
            subText={this.state.additionalInfo.subText}
          />
        ) */}
      </StyledTable>
    );
  }

  private getSharePointHome = () => {
    const { env } = this.props;
    SharePointHome.get(env.wpAjaxAdminUrl, env.nonce)
      .then((response: any) => {
        if (response && response.data && response.data.webUrl) {
          this.setState({
            sharePointHome: response.data.webUrl,
            sharePointHomeError: false
          });
        } else {
          if (response instanceof Error) {
            throw response;
          }
        }
      })
      .catch((error: Error) => {
        const message = `Could not request SharePoint Home from Microsoft Graph (Reason: ${
          error.message
        }). Please enter the your SharePoint Home URL e.g. https://yourtenantname.sharepoint.com/ manually.`;
        alert(message);
        this.setState({ sharePointHomeError: true });
      });
  };

  private copyShortcode = () => {
    (document.getElementsByClassName('pintraSpoShortCode')[0] as HTMLTextAreaElement).select();
    document.execCommand('copy');
    this.setState({
      copyButtonCaption: Strings.capCopyShortcodeButtonSuccess
    });
    setTimeout(() => this.setState({ copyButtonCaption: Strings.capCopyShortcodeButton }), 3000);
  };

  private currentScriptSrc = (scriptName: string): string | null => {
    const scriptTags = document.getElementsByTagName('script');
    for (let i = scriptTags.length - 1; i >= 0; i--) {
      if (scriptTags[i].src.indexOf(scriptName) > -1) {
        return scriptTags[i].src;
      }
    }
    return null;
  };

  private getPluginServerRelativeUrl = () => {
    const loc = window.location.href;
    const wpHome = loc.substr(0, loc.indexOf('/wp-admin'));
    return `${wpHome}/wp-content/plugins/wpo365-spo-premium/`;
  };
}
