import * as React from 'react';

import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledTextArea,
  StyledRowifier,
  StyledErrorFor,
  StyledP
} from './styled';

import { IEnv } from './Wizard';
import { Settings } from '../common/Settings';
import { Strings } from '../strings';
import { PrimaryButton } from 'office-ui-fabric-react/lib/Button';
import { SaveStatus, PluginVersion } from '../common/Enums';
import { errorFor } from '../common/Helpers';
import { SettingsException } from '../common/Exceptions';
import { Footer } from './Footer';
import { VersionSwitch } from './VersionSwitch';
import { Equal } from './Equal';

export interface IErrorsTabProps {
  env: IEnv;
  settings: Settings;
}

export interface IErrorTabsState {
  wpoErrorCheckLog: string;
  wpoErrorNotConfigured: string;
  wpoErrorNotInGroup: string;
  wpoErrorTamperedWith: string;
  wpoErrorUserNotFound: string;
  wpoErrorIdTokenError: string;
  wpoErrorDualLogin: string;
  wpoErrorDualLoginV2: string;
  // ---
  errors: SettingsException[];
  saveStatus: SaveStatus;
}

export class ErrorsTab extends React.Component<IErrorsTabProps, IErrorTabsState> {
  public constructor(props: IErrorsTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      wpoErrorCheckLog: settings.wpoErrorCheckLog,
      wpoErrorNotConfigured: settings.wpoErrorNotConfigured,
      wpoErrorNotInGroup: settings.wpoErrorNotInGroup,
      wpoErrorTamperedWith: settings.wpoErrorTamperedWith,
      wpoErrorUserNotFound: settings.wpoErrorUserNotFound,
      wpoErrorIdTokenError: settings.wpoErrorIdTokenError,
      wpoErrorDualLogin: settings.wpoErrorDualLogin,
      wpoErrorDualLoginV2: settings.wpoErrorDualLoginV2,
      errors: [],
      saveStatus: SaveStatus.initial
    };
  }

  public componentDidUpdate(prevProps: IErrorsTabProps): void {
    if (this.props.settings === prevProps.settings) return;
    const { settings } = this.props;
    this.setState({
      wpoErrorCheckLog: settings.wpoErrorCheckLog,
      wpoErrorNotConfigured: settings.wpoErrorNotConfigured,
      wpoErrorNotInGroup: settings.wpoErrorNotInGroup,
      wpoErrorTamperedWith: settings.wpoErrorTamperedWith,
      wpoErrorUserNotFound: settings.wpoErrorUserNotFound,
      wpoErrorIdTokenError: settings.wpoErrorIdTokenError,
      wpoErrorDualLogin: settings.wpoErrorDualLogin,
      wpoErrorDualLoginV2: settings.wpoErrorDualLoginV2
    });
  }

  public render(): React.ReactElement<IErrorsTabProps> {
    return (
      <StyledTable>
        <StyledTableCaption>
          <StyledTableHeader>Error message</StyledTableHeader>
          <StyledTableIntro
            dangerouslySetInnerHTML={{
              __html: `Use this tab to customize error messages.`
            }}
          />
        </StyledTableCaption>
        <StyledTableRowGroup>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Check log</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledTextArea
                value={this.state.wpoErrorCheckLog}
                onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                  this.setState({ wpoErrorCheckLog: event.target.value })
                }
              />
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>User not in O365 group</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledTextArea
                value={this.state.wpoErrorNotInGroup}
                onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                  this.setState({ wpoErrorNotInGroup: event.target.value })
                }
              />
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>Tampered with</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledTextArea
                value={this.state.wpoErrorTamperedWith}
                onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                  this.setState({ wpoErrorTamperedWith: event.target.value })
                }
              />
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>User not found</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledTextArea
                value={this.state.wpoErrorUserNotFound}
                onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                  this.setState({ wpoErrorUserNotFound: event.target.value })
                }
              />
            </StyledTableCell>
          </StyledTableRow>
          <StyledTableRow>
            <StyledTableCell>
              <StyledLabel>ID token</StyledLabel>
            </StyledTableCell>
            <StyledTableCell>
              <StyledTextArea
                value={this.state.wpoErrorIdTokenError}
                onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                  this.setState({ wpoErrorIdTokenError: event.target.value })
                }
              />
            </StyledTableCell>
          </StyledTableRow>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[PluginVersion.wpo365LoginProfessional, PluginVersion.wpo365LoginPremium, PluginVersion.wpo365LoginIntranet]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Dual login</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledTextArea
                  value={this.state.wpoErrorDualLogin}
                  onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                    this.setState({ wpoErrorDualLogin: event.target.value })
                  }
                />
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <VersionSwitch
            pluginVersion={this.props.env.props.pluginVersion}
            targets={[PluginVersion.wpo365LoginProfessional, PluginVersion.wpo365LoginPremium, PluginVersion.wpo365LoginIntranet]}>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Dual login V2</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledTextArea
                  value={this.state.wpoErrorDualLoginV2}
                  onChange={(event: React.ChangeEvent<HTMLTextAreaElement>) =>
                    this.setState({ wpoErrorDualLoginV2: event.target.value })
                  }
                />
              </StyledTableCell>
            </StyledTableRow>
          </VersionSwitch>
          <StyledTableRow>
            <StyledTableCell />
            <StyledTableCell>
              <StyledRowifier>
                <PrimaryButton
                  onClick={() => this.saveConfiguration()}
                  iconProps={{
                    iconName: this.state.saveStatus == SaveStatus.success ? 'Like' : 'Dislike'
                  }}
                  style={{ minWidth: '400px', minHeight: '30px' }}
                  disabled={this.state.saveStatus == SaveStatus.saving}>
                  Save configuration
                </PrimaryButton>
                <StyledErrorFor>
                  {errorFor(this.state.errors, 'saveConfiguration').map((error: SettingsException) => (
                    <StyledP>{error.message}</StyledP>
                  ))}
                  {this.state.saveStatus == SaveStatus.success ? Strings.okSettingsUpdated : null}
                </StyledErrorFor>
              </StyledRowifier>
            </StyledTableCell>
          </StyledTableRow>
        </StyledTableRowGroup>
        <Footer />
      </StyledTable>
    );
  }

  private async saveConfiguration(): Promise<void> {
    this.setState({ saveStatus: SaveStatus.saving });
    const { settings } = this.props;
    settings.wpoErrorCheckLog = this.state.wpoErrorCheckLog;
    settings.wpoErrorNotConfigured = this.state.wpoErrorNotConfigured;
    settings.wpoErrorNotInGroup = this.state.wpoErrorNotInGroup;
    settings.wpoErrorTamperedWith = this.state.wpoErrorTamperedWith;
    settings.wpoErrorUserNotFound = this.state.wpoErrorUserNotFound;
    settings.wpoErrorIdTokenError = this.state.wpoErrorIdTokenError;
    settings.wpoErrorDualLogin = this.state.wpoErrorDualLogin;
    settings.wpoErrorDualLoginV2 = this.state.wpoErrorDualLoginV2;
    const updateResult = await Settings.updateSettings(this.props.env, settings);

    if (updateResult.success) {
      this.setState({
        errors: [],
        saveStatus: SaveStatus.success
      });
    } else {
      this.setState((prevState: IErrorTabsState, props: IErrorsTabProps) => {
        return {
          errors: [...prevState.errors, new SettingsException('saveConfiguration', updateResult.message, 2000)],
          saveStatus: SaveStatus.error
        };
      });
    }
  }
}
