import * as React from 'react';

import {
  StyledList,
  StyledListRow,
  StyledIconButton,
  StyledRowifier,
  StyledErrorFor,
  StyledP
} from './styled';

import { KeyValuePair } from '../common/KeyValuePair';

import { Icon } from 'office-ui-fabric-react/lib/Icon';
import { IconNames } from '@uifabric/icons';
import { ValidatableTextInput } from './ValidatableTextInput';
import { SettingsException } from '../common/Exceptions';
import { Strings } from '../strings';
import { Size } from '../common/Enums';

export interface IKeyValueEditorProps {
  onListChanged: (list: KeyValuePair[]) => void;
  list: KeyValuePair[];
  keyPlaceholder?: string;
  keyIsValid?: RegExp;
  keyErrorMessage?: string;
  valuePlaceholder?: string;
  valueIsValid?: RegExp;
  valueErrorMessage?: string;
}

export interface IKeyValueEditorState {
  list: KeyValuePair[];
  errors: SettingsException[];
  keyIsValid: boolean;
  valueIsValid: boolean;
  value: KeyValuePair;
}

export class KeyValueEditor extends React.Component<
  IKeyValueEditorProps,
  IKeyValueEditorState
> {
  public constructor(props: IKeyValueEditorProps) {
    super(props);
    this.state = {
      list: this.props.list,
      errors: [],
      keyIsValid: true,
      valueIsValid: true,
      value: {
        key: Strings.empty,
        value: Strings.empty
      }
    };
  }

  public componentDidUpdate(prevProps: IKeyValueEditorProps): void {
    if (this.props.list === prevProps.list) return;
    this.setState({ list: this.props.list });
  }

  public render(): React.ReactElement<IKeyValueEditorProps> {
    const {
      keyIsValid,
      keyErrorMessage,
      valueIsValid,
      valueErrorMessage,
      keyPlaceholder,
      valuePlaceholder
    } = this.props;
    return (
      <StyledRowifier>
        <StyledList>
          {this.state.list.map((item: KeyValuePair, idx: number) => {
            return (
              <StyledListRow key={idx}>
                <ValidatableTextInput
                  size={Size.small}
                  defaultValue={item.key}
                  value={item.key}
                  bound={true}
                  disabled={true}
                />
                <ValidatableTextInput
                  size={Size.small}
                  defaultValue={item.value}
                  value={item.value}
                  bound={true}
                  disabled={true}
                />
                <StyledIconButton onClick={() => this.deleteItem(idx)}>
                  <Icon
                    iconName={IconNames.Delete}
                    title={'Delete'}
                    ariaLabel={'Click to delete element'}
                    style={{
                      fontSize: '12px',
                      lineHeight: '30px',
                      height: '30px',
                      width: '30px',
                      margin: '0px 0px 0px -30px',
                      padding: 0,
                      cursor: 'pointer'
                    }}
                  />
                </StyledIconButton>
              </StyledListRow>
            );
          })}
          <StyledListRow>
            <ValidatableTextInput
              size={Size.small}
              defaultValue={Strings.empty}
              value={this.state.value.key}
              bound={true}
              isValid={(oldValue: any, newValue: any) => {
                if (
                  keyIsValid &&
                  newValue &&
                  (newValue as string) != Strings.empty
                )
                  return keyIsValid.test(newValue as string);
                return true;
              }}
              onValid={this.onKeyValid}
              onError={this.onKeyError}
              errorMessage={keyErrorMessage}
              placeholder={keyPlaceholder}
            />
            <ValidatableTextInput
              size={Size.small}
              defaultValue={Strings.empty}
              value={this.state.value.value}
              bound={true}
              isValid={(oldValue: any, newValue: any) => {
                if (
                  valueIsValid &&
                  newValue &&
                  (newValue as string) != Strings.empty
                )
                  return valueIsValid.test(newValue as string);
                return true;
              }}
              onValid={this.onValueValid}
              onError={this.onValueError}
              errorMessage={valueErrorMessage}
              placeholder={valuePlaceholder}
            />
            <StyledIconButton onClick={this.addItem}>
              <Icon
                iconName={IconNames.Add}
                title={'Add'}
                ariaLabel={'Click to add this item'}
                style={{
                  fontSize: '12px',
                  lineHeight: '30px',
                  height: '30px',
                  width: '30px',
                  margin: '0px 0px 0px -30px',
                  padding: 0,
                  cursor: 'pointer'
                }}
              />
            </StyledIconButton>
          </StyledListRow>
        </StyledList>
        {this.props.children}
        <StyledErrorFor>
          {this.state.errors.map((error: SettingsException) => (
            <StyledP>{error.message}</StyledP>
          ))}
        </StyledErrorFor>
      </StyledRowifier>
    );
  }

  private onKeyError = (errorMessage: string) => {
    this.setState({
      errors: [new SettingsException(null, errorMessage)],
      keyIsValid: false
    });
  };

  private onValueError = (errorMessage: string) => {
    this.setState({
      errors: [new SettingsException(null, errorMessage)],
      valueIsValid: false
    });
  };

  private onKeyValid = (newValue: any) => {
    this.setState((prevState: IKeyValueEditorState) => ({
      keyIsValid: true,
      value: {
        key: newValue as string,
        value: prevState.value.value
      },
      errors: []
    }));
  };

  private onValueValid = (newValue: any) => {
    this.setState((prevState: IKeyValueEditorState) => ({
      valueIsValid: true,
      value: {
        key: prevState.value.key,
        value: newValue as string
      },
      errors: []
    }));
  };

  private addItem = () => {
    const { list } = this.state;
    if (
      this.state.keyIsValid &&
      this.state.value.key != Strings.empty &&
      this.state.valueIsValid &&
      this.state.value.value != Strings.empty
    ) {
      const newList = [...list, this.state.value];
      this.setState({
        list: newList,
        value: {
          key: '',
          value: ''
        }
      });
      this.props.onListChanged(newList);
    }
  };

  private deleteItem = (idx: number) => {
    const newList = [...this.state.list];
    newList.splice(idx, 1);
    this.setState({ list: newList });
    this.props.onListChanged(newList);
  };
}
