import styled, { StyledFunction, createGlobalStyle } from 'styled-components';

export const GlobalWizardStyled = createGlobalStyle`
  // Hide the pivot menu bar
  .ms-Pivot {
    display: none;
  }
  .pintraOverflowMenu {
    margin-top: 1rem;
  }
`;

export const StyledBorderBox = styled.div`
  box-sizing: border-box;
`;

export const StyledTable = styled(StyledBorderBox)`
  display: table;
  width: calc(100% - 35px);
  margin: 15px 15px 15px 0px;
  margin-left: 0px;
  padding-left: 5px;
  background-color: rgb(0, 120, 212);
  box-shadow: 0 1px 1px 0 rgba(0, 0, 0, 0.1);
`;

export const StyledTableCaption = styled(StyledBorderBox)`
  display: table-caption;
  background-color: rgb(0, 120, 212);
`;

export const StyledTableHeader = styled(StyledBorderBox)`
  background-color: #ffffff;
  margin-left: 5px;
  padding: 15px;
  font-size: 130%;
  font-weight: bold;
`;

export const StyledTableIntro = styled(StyledBorderBox)`
  background-color: #ffffff;
  margin-left: 5px;
  padding: 15px;
`;

export const StyledTableRowGroup = styled(StyledBorderBox)`
  display: table-row-group;
`;

export const StyledTableRow = styled(StyledBorderBox)`
  display: table-row;
  background-color: #ffffff;
`;

export const StyledTableCell = styled(StyledBorderBox)`
  display: table-cell;
  padding: 3px;
  vertical-align: top;
  text-align: left;
  &:first-child {
    padding-left: 15px;
    width: 25%;
    min-width: 300px;
  }
  &:nth-child(2) {
    width: 65%;
    min-width: 600px;
  }
`;

export const StyledTableFooter = styled(StyledTableCell)`
  text-align: center;
  overflow: visible;
  white-space: nowrap;
  max-width: 1px;
  font-size: 90%;
  font-weight: 600;
  padding-top: 30px;
  padding-bottom: 15px;
`;

export const StyledLabel = styled(StyledBorderBox)`
  height: 30px;
  line-height: 30px;
  white-space: nowrap;
`;

export const StyledP = styled.p`
  box-sizing: border-box;
  line-height: 30px;
  margin: 0px !important;
  padding: 0px !important;
`;

export interface IStyledInputProps {
  minWidth: string;
}

const input: StyledFunction<IStyledInputProps & React.HTMLProps<HTMLInputElement>> = styled.input;

export const StyledInputText = input`
  min-width: ${props => props.minWidth};
  width: ${props => props.minWidth};
  padding: 2px;
  height: 30px;
  line-height: 30px;
  border: 1px solid rgb(51, 51, 51) !important;
`;

export const StyledErrorFor = styled.label`
  padding-left: 15px;
  height: 30px;
  line-height: 30px;
  color: #cc0000;
`;

export const StyledOauthUrl = styled.textarea`
  min-width: 400px;
  min-height: 100px;
  padding: 2px;
`;

export const StyledSettings = styled.textarea`
  min-width: 400px;
  min-height: 100px;
  padding: 2px;
  font-size: 80%;
`;

export const StyledTextArea = styled.textarea`
  min-width: 400px;
  min-height: 100px;
  padding: 2px;
`;

export const StyledCopyButton = styled.button`
  min-width: 400px;
  min-height: 30px;
  padding: 2px;
`;

export const StyledIconButton = styled(StyledBorderBox)`
  font-size: 12px;
  line-height: 30px;
  height: 30px;
  z-index: 99;
`;

export const StyledCheckbox = styled(StyledBorderBox)`
  padding: 5px 0px;
  height: 30px;
  line-height: 30px;
`;

export const StyledRowifier = styled(StyledBorderBox)`
  display: flex;
  align-items: start;
`;

export const StyledCallout = styled(StyledBorderBox)`
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-width: 600px;
  margin: 5px;
  img {
    width: 100% !important;
    height: auto !important;
  }
`;

export const StyledCalloutTitle = styled(StyledBorderBox)`
  flex-growth: 1;
  flex-shrink: 0;
  width: 100%;
  padding: 1rem 1rem 0.5rem 1rem;
  font-size: 2rem;
  font-weight: 200;
`;

export const StyledCalloutContent = styled(StyledBorderBox)`
  flex-growth: 1;
  flex-shrink: 0;
  width: 100%;
  padding: 0.5rem 1rem;
`;

export const StyledCalloutFooter = styled(StyledBorderBox)`
  flex-growth: 1;
  flex-shrink: 0;
  width: 100%;
  padding: 0.5rem 1rem 1rem 1rem;
`;

export const StyledList = styled(StyledBorderBox)`
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
`;

export const StyledListRow = styled(StyledBorderBox)`
  display: flex;
  align-items: center;
  height: 36px;
  line-height: 30px;
  flex-grow: 1;
  flex-shrink: 0;
  padding: 0px 0px 3px 0px;
`;

export const StyledListWrapper = styled.div`
  display: flex;
  flex-direction: column;
`;

export const StyledListMenu = styled.div``;

export const StyledListMain = styled.div``;

export const StyledLogItemCell = styled.div`
  display: flex;
  flex-wrap: nowrap;
`;

export const StyledLogItemTimeStamp = styled.div`
  white-space: nowrap;
  padding: 2px;
`;

export const StyledLogItemType = styled.div`
  white-space: nowrap;
  padding: 2px;
`;

export const StyledLogItemMessage = styled.div`
  padding: 2px;
  word-wrap: break-word;
  overflow: auto;
`;
