import { SettingsException } from './Exceptions';
import Axios from 'axios';

export function randomString(length: number) {
  var text = '';
  var possible =
    'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
  for (var i = 0; i < length; i++) {
    text += possible.charAt(Math.floor(Math.random() * possible.length));
  }
  return text;
}

export function errorFor(
  errors: SettingsException[],
  fieldName: string
): SettingsException[] {
  return errors.filter(
    (error: SettingsException) => error.errorFor == fieldName
  );
}

export async function getRenderedPost(slug: string): Promise<string | Error> {
  let html: string = '';

  try {
    const response = await Axios.get(
      `https://www.wpo365.com/wp-json/wp/v2/posts?slug=${slug}`
    );

    if (
      response.data &&
      Array.isArray(response.data) &&
      response.data.length == 1 &&
      response.data[0].content &&
      response.data[0].content.rendered
    ) {
      html = response.data[0].content.rendered;
    }
    return html;
  } catch (error) {
    return error;
  }
}
