import * as React from 'react';

import { IEnv } from './Wizard';

import { Settings } from '../common/Settings';

import {
  StyledTable,
  StyledTableCaption,
  StyledTableHeader,
  StyledTableIntro,
  StyledTableRowGroup,
  StyledTableRow,
  StyledTableCell,
  StyledLabel,
  StyledRowifier,
  StyledSettings
} from './styled';

import { Footer } from './Footer';

import { DefaultButton } from 'office-ui-fabric-react/lib/Button';
import { Strings } from '../strings';
import { SaveStatus } from '../common/Enums';
import { SettingsException } from '../common/Exceptions';

export interface IExportImportTabProps {
  env: IEnv;
  settings: Settings;
  getSettings: () => void;
}

export interface IExportImportTabState {
  localSettings: Settings;
  copyButtonCaption: string;
  saveButtonCaption: string;
  // ---
  errors: SettingsException[];
  saveStatus: SaveStatus;
}

export class ExportImportTab extends React.Component<IExportImportTabProps, IExportImportTabState> {
  private settingsTextArea: any;

  public constructor(props: IExportImportTabProps) {
    super(props);
    const { settings } = this.props;
    this.state = {
      localSettings: settings,
      copyButtonCaption: Strings.capCopySettingsButton,
      saveButtonCaption: Strings.capSaveSettingsButton,
      errors: [],
      saveStatus: SaveStatus.initial
    };
  }

  public componentDidUpdate(prevProps: IExportImportTabProps): void {
    const { settings } = this.props;
    if (prevProps.settings != settings) {
      this.setState({
        localSettings: settings
      });
    }
  }

  public render(): React.ReactElement<IExportImportTabProps> {
    const { localSettings, copyButtonCaption, saveButtonCaption } = this.state;
    return (
      <React.Fragment>
        <StyledTable>
          <StyledTableCaption>
            <StyledTableHeader>{Strings.titleImportExport}</StyledTableHeader>
            <StyledTableIntro
              dangerouslySetInnerHTML={{
                __html: `<strong>Use this tab to</strong>
                <ul>
                <li>Backup settings by copying them to the clipboard and save them using your favorite editor</li>
                <li>Restore a backup from your clipboard by toggling 'Let me paste / save updated settings'</li>
                <li>Create a copy of your settings when contacting support <a href="https://www.wpo365.com/contact/">support</a></li>
                </ul>
                <strong>Don't Use this tab to</strong>
                <ul>
                <li>Edit settings in the editor below (this is not supported and it will fail)</li>
                <li>Remove settings in the editor below (this is not supported)</li>
                </ul>`
              }}
            />
          </StyledTableCaption>
          <StyledTableRowGroup>
            <StyledTableRow>
              <StyledTableCell>
                <StyledLabel>Settings:</StyledLabel>
              </StyledTableCell>
              <StyledTableCell>
                <StyledSettings
                  ref={(textarea: any) => (this.settingsTextArea = textarea)}
                  readOnly={true}
                  value={JSON.stringify(localSettings, null, 2)}
                  onChange={this.settingsUpdated}
                />
              </StyledTableCell>
            </StyledTableRow>
            <StyledTableRow>
              <StyledTableCell />
              <StyledTableCell>
                <StyledRowifier>
                  <DefaultButton
                    onClick={this.copyToClipboard}
                    iconProps={{ iconName: 'Copy' }}
                    style={{ minWidth: '400px', minHeight: '30px' }}>
                    {copyButtonCaption}
                  </DefaultButton>
                </StyledRowifier>
              </StyledTableCell>
            </StyledTableRow>
          </StyledTableRowGroup>
          <Footer />
        </StyledTable>
      </React.Fragment>
    );
  }

  // Updates the local copy of the settings object

  private settingsUpdated = (event: React.FormEvent<HTMLTextAreaElement>): void => {
    this.setState({ localSettings: JSON.parse((event.target as any).value) });
  };

  // Copies the settings to the clipboard and shows a message during 5 seconds

  private copyToClipboard = (event: React.MouseEvent<HTMLButtonElement>): void => {
    this.settingsTextArea.select();
    document.execCommand('copy');
    (event.target as any).focus();
    this.setState({
      copyButtonCaption: Strings.capCopySettingsButtonSuccess
    });
    setTimeout(() => this.setState({ copyButtonCaption: Strings.capCopySettingsButton }), 3000);
  };
}
