import { MsGraphVersion, AuthScenario, ReplaceOrAddRoles } from './Enums';
import { SettingsResponse } from './Responses';
import Axios from 'axios';
import { IEnv } from '../components/Wizard';
import { randomString } from './Helpers';
import { KeyValuePair } from './KeyValuePair';
import { Strings } from '../strings';
import { SettingsException } from './Exceptions';

export class Settings {
  public aadResourceUri: string = '00000002-0000-0000-c000-000000000000';
  public allGroupMemberships: boolean = false;
  public alwaysUseGotoAfter: boolean = false;
  public applicationId: string = '';
  public applicationSecret: string = '';
  public authScenario: AuthScenario = AuthScenario.intranet;
  public avatarUpdated: number = 259200;
  public blockEmailChange: boolean = true;
  public blockPasswordChange: boolean = true;
  public createAndAddUsers: boolean = true;
  public customDomain: string[] = [];
  public customLoginUrl: string = '';
  public debugLog: boolean = false;
  public debugLogIdToken: boolean = false;
  public defaultDomain: string = '';
  public defaultRoleAsFallback: boolean = true;
  public domainHint: string = '';
  public domainWhitelist: string[] = [];
  public domainsXRoles: KeyValuePair[] = [];
  public enableNonceCheck: boolean = false;
  public enableSingleSignOut: boolean = false;
  public enableTokenService: boolean = false;
  public enableUserSync: boolean = false;
  public extraUserFields: KeyValuePair[] = [];
  public errorPageUrl: string = '';
  public gotoAfterSignonUrl: string = '';
  public graphUserDetails: boolean = false;
  public graphVersion: MsGraphVersion = MsGraphVersion.current;
  public groupsWhitelist: string[] = [];
  public groupsXRoles: KeyValuePair[] = [];
  public groupsXGotoAfter: KeyValuePair[] = [];
  public hideErrorNotice: boolean = false;
  public hideSsoLink: boolean = false;
  public interceptWpLogin: boolean = false;
  public leeway: number = 300;
  public licenseKey: string = '';
  public logoutFromO365: boolean = false;
  public muNewUsrDefaultRole: string = 'subscriber';
  public multiTenanted: boolean = false;
  public newUsrDefaultRole: string = 'subscriber';
  public newUsrSendMail: boolean = false;
  public newUsrSendMailAdminOnly: boolean = false;
  public newUsrSendMailCustom: boolean = false;
  public newUsrMailSubject: string = '[__##BLOG_NAME##__] New user registration';
  public newUsrMailTitle: string = 'NEW USER REGISTRATION';
  public newUsrMailSalutation: string = 'Dear __##USER_DISPLAY_NAME##__';
  public newUsrMailBody: string =
    'The website <strong><a href="__##BLOG_URL##__">__##BLOG_NAME##__</a></strong> has been shared with you. You can now sign in using your Office 365 account <strong>__##USER_LOGIN_NAME##__</strong>.';
  public newUsrMailFooter: string =
    'Powered by <a href="https://www.wpo365.com/" target="_blank">WPO365 - Connecting Wordpress and Office 365</a>';
  public nonceSecret: string = randomString(16);
  public pagesBlacklist: string[] = ['wp-login.php', '/login/', 'wp-cron.php', 'admin-ajax.php', 'xmlrpc.php'];
  public postSignoutUrl: string = '';
  public privatePages: string[] = [];
  public publicHomepage: boolean = false;
  public redirectUrl: string = '';
  public redirectToLogin: boolean = false;
  public redirectToLoginV2: boolean = false;
  public replaceOrUpdateUserRoles: ReplaceOrAddRoles = ReplaceOrAddRoles.add;
  public sessionDuration: number = 3600;
  public skipHostVerification: boolean = true;
  public skipApiBasicAuthRequest: boolean = false;
  public skipNonceVerification: boolean = false;
  public skipUserRegisterAction: boolean = false;
  public skipWpLoginAction: boolean = false;
  public tenantId: string = '';
  public useAvatar: boolean = false;
  public useBpAvatar: boolean = false;
  public useIdTokenParserV1: boolean = false;
  public useV2: boolean = true;
  public userSyncQuery: string = `myorganization/users?$filter=accountEnabled+eq+true+and+userType+eq+%27member%27&$top=10`;
  public welcomePageUrl: string = '';
  public wpo365LoginDownloadLink: string = '';
  public wpoErrorCheckLog: string = Strings.wpoErrorCheckLog;
  public wpoErrorNotConfigured: string = Strings.wpoErrorNotConfigured;
  public wpoErrorNotInGroup: string = Strings.wpoErrorNotInGroup;
  public wpoErrorTamperedWith: string = Strings.wpoErrorTamperedWith;
  public wpoErrorUserNotFound: string = Strings.wpoErrorUserNotFound;
  public wpoErrorIdTokenError: string = Strings.wpoErrorIdTokenError;
  public wpoErrorDualLogin: string = Strings.wpoErrorDualLogin;
  public wpoErrorDualLoginV2: string = Strings.wpoErrorDualLoginV2;

  public constructor(env: IEnv) {
    this.redirectUrl = env.props.siteUrl.substr(-1) != '/' ? env.props.siteUrl + '/' : env.props.siteUrl;
  }

  public static async getSettings(env: IEnv): Promise<Settings> {
    const data = new FormData();
    data.append('action', 'get_settings');
    data.append('nonce', env.nonce);

    let response: any;
    try {
      response = await Axios.post(env.wpAjaxAdminUrl, data);
    } catch (error) {
      console.log(error);
      return null;
    }
    if (response.status == 200) {
      if (response.data && response.data.status && response.data.status == 'OK') {
        try {
          const result = JSON.parse(response.data.result);
          const settings: any = new Settings(env);
          Object.keys(settings).forEach((key: string) => {
            if (typeof result[key] !== 'undefined' && result[key] !== null) settings[key] = result[key];
          });
          return settings;
        } catch (error) {
          console.log(error);
          return null;
        }
      }
    }
    return null;
  }

  public static async updateSettings(env: IEnv, settings: Settings): Promise<SettingsResponse> {
    const data = new FormData();
    data.append('action', 'update_settings');
    data.append('nonce', env.nonce);
    data.append('settings', btoa(JSON.stringify(settings)));

    let response: any;
    try {
      response = await Axios.post(env.wpAjaxAdminUrl, data);
    } catch (error) {
      return error;
    }
    if (response.status == 200) {
      if (response.data && response.data.status) {
        if (response.data.status == 'OK') {
          return {
            error: null,
            message: Strings.empty,
            success: true
          };
        }
        return {
          error: new SettingsException(null, response.data.message),
          message: Strings.empty,
          success: false
        };
      }
    }
    return {
      error: new SettingsException(null, response.message),
      message: Strings.empty,
      success: false
    };
  }

  public update(): Promise<SettingsResponse> {
    return null;
  }

  public validate(): Promise<SettingsResponse> {
    return null;
  }
}
