import { IEnv } from '../components/Wizard';
import Axios from 'axios';

export class Log {
  public static async getLog(env: IEnv): Promise<string> {
    const data = new FormData();
    data.append('action', 'get_log');
    data.append('nonce', env.nonce);

    let response: any;
    try {
      response = await Axios.post(env.wpAjaxAdminUrl, data);
    } catch (error) {
      console.log(error);
      return null;
    }
    if (response.status == 200) {
      if (
        response.data &&
        response.data.status &&
        response.data.status == 'OK'
      ) {
        try {
          const log = JSON.parse(response.data.result);
          return log;
        } catch (error) {
          console.log(error);
          return null;
        }
      }
    }
    return null;
  }
}
